.price-table {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }

  .prices + & {
    margin-top: -80px;

    @media (max-width: $extra-small) {
      margin-top: -50px;
    }
  }

  & + & {
    margin-top: -40px;

    @media (max-width: $large) {
      margin-top: -60px;
    }

    @media (max-width: $medium) {
      margin-top: -40px;
    }

    @media (max-width: $extra-small) {
      margin-top: 90px;
    }
  }
}

.price-table__title {
  margin-bottom: 90px;

  @media (max-width: $extra-large) {
    margin-bottom: 60px;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 40px;
  }

  span {
    display: block;
  }
}

.price-table__list {
  @include list-reset();
  counter-reset: price-list;
  border-bottom: 1px solid #bcbcbc;
}

.price-table__link {
  @include link-reset();
  position: relative;
  display: grid;
  grid-template-columns: 192px auto auto 100px;
  align-items: center;
  width: 100%;
  padding: 41px 0 40px;
  border-top: 1px solid #bcbcbc;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: $color-text--light;

  @media (max-width: $extra-large) {
    grid-template-columns: 100px auto auto 80px;
  }

  @media (max-width: $large) {
    grid-template-columns: 100px auto auto 80px;
  }

  @media (max-width: $medium) {
    display: block;
    padding: 40px 0 40px 100px;
  }

  @media (max-width: $small) {
    padding: 20px 0 20px 80px;
  }

  @media (max-width: $extra-small) {
    padding: 19px 0 25px 45px;
  }

  &:hover {
    color: $color-main;
  }

  &::before {
    @include accent-font(30px, 20px, 1.1, 400);
    content: "(0" counter(price-list) ")";
    counter-increment: price-list;
    align-self: flex-start;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    @media (max-width: $medium) {
      position: absolute;
      top: 50px;
      left: 0;
    }

    @media (max-width: $small) {
      top: 40px;
    }

    @media (max-width: $extra-small) {
      top: 19px;
    }
  }
}

.price-table__name {
  @include reset();
  @include accent-font(30px, 20px, 1.1, 400);
  max-width: 500px;
  padding-right: 40px;
  letter-spacing: 0.04em;

  @media (max-width: $medium) {
    margin-bottom: 10px;
    letter-spacing: 0.02em;
  }
}

.price-table__value {
  display: flex;
  align-items: baseline;
  justify-self: flex-end;
  gap: 30px;

  @media (max-width: $small) {
    gap: 20px;
  }
}

.price-table__old-value {
  @include reset();
  @include main-font(20px, 16px, 1.6, 500);
  flex-shrink: 0;
  letter-spacing: 0.02em;
  text-decoration: line-through;
  color: #9f9f9f;
}

.price-table__new-value {
  @include reset();
  @include main-font(24px, 20px, 1.6, 500);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-shrink: 0;
  min-width: 173px;
  letter-spacing: 0.045em;
  text-transform: uppercase;
  color: $color-main;

  @media (max-width: $medium) {
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    min-width: 0;
  }

  @media (max-width: $small) {
    flex-direction: column;
    align-items: flex-start;
  }

  span {
    @include main-font(13px, 13px, 15px, 500);
    display: block;
    padding: 5px 22px;
    color: $white;
    background: $color-main;
    letter-spacing: 0.01em;
    text-transform: none;
    border-radius: 62px;

    @media (max-width: $medium) {
      margin-left: 20px;
    }

    @media (max-width: $small) {
      margin-left: 0;
    }

    @media (max-width: $extra-small) {
      padding: 5px 10px;
    }
  }
}

.price-table__arrow {
  @media (max-width: $medium) {
    display: none;
  }
}

.price-table__arrow-icon {
  display: block;
  margin-left: auto;
  opacity: 0;
  transform: translate(-50%);
  fill: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $medium) {
    opacity: 1;
  }

  @media (max-width: $small) {
    width: 36px;
    height: auto;
  }

  @media (max-width: $extra-small) {
    transform: translate(0);
  }

  .price-table__link:hover .price-table__arrow & {
    opacity: 1;
    transform: translate(0);
  }
}

.price-table__footer {
  display: flex;
  justify-content: center;
  margin-top: 95px;

  @media (max-width: $extra-large) {
    margin-top: 60px;
  }

  @media (max-width: $small) {
    margin-top: 40px;
  }

  @media (max-width: $extra-small) {
    justify-content: flex-start;
    margin-top: 30px;
  }
}

.price-table__footer-button {
  @media (max-width: $extra-small) {
    width: 139px;
    height: 46px;
  }
}
