@keyframes scroll {
  0% {
    transform: translateY(-15px);
    opacity: 0;
  }
  45% {
    transform: translateY(0px);
    opacity: 1;
  }
  65% {
    transform: translateY(-5px);
    opacity: 1;
  }
  100% {
    transform: translateY(15px);
    opacity: 0;
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
