.team-member {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.team-member__post {
  height: 2em;
  margin-bottom: 12px;
  line-height: 1;
}

.team-member__info {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin-top: auto;
  padding-left: 37px;

  @media (max-width: $large) {
    padding-left: 30px;
  }
}

.team-member__wrap {
  position: relative;
  margin-bottom: 20px;

  @media (max-width: $extra-small) {
    margin-bottom: 14px;
  }
}

.team-member__image-wrap {
  position: relative;
  width: 100%;
  padding-bottom: 121%;
  border-radius: 0 0 50px 0;
  overflow: hidden;
}

.team-member__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top center;
}

.team-member__link {
  @include link-reset();
  @include main-font(14px, 13px, 1.14, 500);
  position: absolute;
  bottom: 0;
  left: -36px;
  writing-mode: tb-rl;
  transform: rotate(180deg);
  letter-spacing: 0.02em;
  color: $color-main;
  text-transform: uppercase;

  @media (max-width: $large) {
    left: -29px;
  }

  &::after {
    @include pseudo(1px, 100%);
    background: $color-main;
    bottom: 0;
    left: 1px;
    transition: $main-transition;
  }

  &:hover {
    color: $color-text;
  }

  &:hover::after {
    background: $color-text;
  }
}

.team-member__name {
  @include reset();
  margin-bottom: 15px;

  @media (max-width: $extra-small) {
    margin-bottom: 8px;
  }
}

.team-member__button {
  align-self: flex-start;
  margin-top: auto;
}
