.reasons {
  margin: 180px 0;
  border-top: 1px solid $color-main;
  border-bottom: 1px solid $color-main;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.reasons__content {
  display: grid;
  grid-template-columns: calc(50% - 162px) 1fr 140px;
  height: 100%;
  min-height: 746px;
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    grid-template-columns: calc(50% - 162px) 1fr 100px;
    margin-right: -40px;
  }

  @media (max-width: $medium) {
    min-height: auto;
    grid-template-columns: 1fr;
    margin: 0 -40px;
  }

  @media (max-width: $small) {
    margin: 0 -20px;
  }

  @media (max-width: 374px) {
    margin: 0 -10px;
  }
}

.reasons__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 40px 50px 50px 0;

  @media (max-width: $extra-large) {
    padding: 40px 30px 50px 0;
  }

  @media (max-width: $large) {
    padding: 40px 20px 50px 0;
  }

  @media (max-width: $medium) {
    max-width: 648px;
    padding: 40px;
  }

  @media (max-width: $small) {
    padding: 40px 20px;
  }

  @media (max-width: 374px) {
    padding: 30px 10px;
  }
}

.reasons__image-wrap {
  position: relative;
  width: 100%;
  margin-bottom: 60px;
  padding-bottom: 75%;
  border-radius: 300px;
  overflow: hidden;

  @media (max-width: $large) {
    margin-bottom: 40px;
  }

  @media (max-width: $medium) {
    margin-bottom: 30px;
    padding-bottom: 67%;
  }

  @media (max-width: $small) {
    margin-bottom: 20px;
  }
}

.reasons__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.reasons__video-link {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.reasons__button-play {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 94px;
  height: 94px;
  border: 1px solid $white;
  border-radius: 50%;
  transition: $main-transition;

  .reasons__video-link:hover & {
    background-color: $white;
  }
}

.reasons__button-play-icon {
  margin-left: 2px;
  fill: $white;
  transition: $main-transition;

  .reasons__video-link:hover & {
    fill: $color-main;
  }
}

.reasons__list {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 75px 0;
  align-content: center;
  padding: 55px 0 0 70px;
  border-left: 1px solid $color-main;
  border-right: 1px solid $color-main;

  @media (max-width: 1800px) {
    gap: 30px 0;
    padding: 55px 0 55px 60px;
  }

  @media (max-width: $large) {
    gap: 20px 0;
    padding: 55px 0 55px 40px;
  }

  @media (max-width: $medium) {
    border-top: 1px solid $color-main;
    border-bottom: 1px solid $color-main;
    border-left: none;
    border-right: none;
  }

  @media (max-width: $small) {
    padding: 50px 0 50px 20px;
  }

  @media (max-width: $extra-small) {
    grid-template-columns: 1fr;
    gap: 50px;
    padding: 50px 0 25px 20px;
  }
}

.reasons__item {
  @media (max-width: $extra-small) {
    position: relative;
    padding-left: 90px;
  }
}

.reasons__icon {
  max-height: 84px;
  margin-bottom: 20px;

  @media (max-width: $extra-small) {
    position: absolute;
    top: 0;
    left: 0;
    max-height: 95px;
  }
}

.reasons__name {
  width: 83%;
  margin-bottom: 20px;

  @media (max-width: $extra-small) {
    width: 97%;
    margin-bottom: 12px;
  }
}

.reasons__text {
  width: 85%;

  @media (max-width: $extra-small) {
    width: 90%;
    line-height: 1.45;
  }
}

.reasons__general-link {
  @include link-reset();
  display: flex;
  align-items: center;
  writing-mode: tb-rl;
  transform: rotate(180deg);

  @media (max-width: $medium) {
    justify-content: center;
    writing-mode: lr-tb;
    transform: none;
    border-right: none;
  }

  span {
    position: relative;
    padding-top: 80px;

    @media (max-width: $medium) {
      display: flex;
      align-items: center;
      padding: 40px 0;
    }

    @media (max-width: $small) {
      padding: 24px 0;
    }
  }

  &:hover {
    background: $color-main;

    span {
      color: $white;
    }
  }
}

.reasons__general-icon-arrow {
  margin-top: 30px;
  fill: $color-main;
  transform: rotate(90deg);
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 37px;
    height: auto;
    margin-top: 20px;
  }

  @media (max-width: $medium) {
    width: 32px;
    margin-top: 0;
    margin-left: 14px;
    transform: rotate(0);
  }

  .reasons__general-link:hover & {
    fill: $white;
    transform: rotate(90deg) translateX(20px);

    @media (max-width: $medium) {
      transform: translateX(20px);
    }
  }
}

.reasons__general-icon-ellipse {
  margin-top: -32px;
  fill: transparent;
  stroke: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 51px;
    height: auto;
    margin-top: -20px;
  }

  @media (max-width: $medium) {
    width: 45px;
    height: auto;
    margin-top: 0;
    margin-left: -27px;
    transform: rotate(90deg);
  }

  .reasons__general-link:hover & {
    stroke: $white;
    transform: rotate(-60deg) translate(10px, -8px);

    @media (max-width: $medium) {
      transform: rotate(30deg);
    }
  }
}
