.info-block {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.info-block__content {
  display: grid;
  grid-template-columns: calc(50% - 62px) auto;

  @media (max-width: $max - 1) {
    grid-template-columns: calc(50% - 101px) auto;
  }

  @media (max-width: $extra-large) {
    grid-template-columns: calc(50% - 142px) auto;
  }

  @media (max-width: $medium) {
    display: block;
  }
}

.info-block__title {
  max-width: 550px;

  @media (max-width: $extra-large) {
    padding-right: 40px;
  }

  @media (max-width: $medium) {
    margin-bottom: 40px;
    padding-right: 0;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 20px;
  }
}

.info-block__text-content{
  position: relative;
  max-width: 500px;
  max-height: 385px;
  letter-spacing: 0.06em;
  overflow: hidden;

  @media (max-width: $medium) {
    max-width: 630px;
  }

  @media (max-width: $extra-small) {
    max-height: 220px;
  }

  &::after {
    @include pseudo(100%, 125px);
    bottom: 0;
    left: 0;
    background: linear-gradient(358.4deg, $white 26.18%, rgba($white, 0) 145.98%);
  }

  &.show {
    max-height: 100vh;
  }

  &.show::after {
    display: none;
  }

  p {
    @include reset();

    @media (max-width: $extra-small) {
      line-height: 1.45;
    }

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  ul {
    @include list-reset();
  }

  li {
    position: relative;
    padding-left: 28px;

    &:not(:last-child) {
      margin-bottom: 12px;
    }

    &::before {
      @include pseudo(12px, 12px);
      top: 7px;
      left: 0;
      border-radius: 50%;
      border: 1px solid $color-text;
    }
  }

  .title {
    @include accent-font(30px, 20px, 1.1, 400);
    max-width: 450px;
    margin: 39px 0 26px;
    text-transform: none;
  }
}

.info-block__button-more {
  @include button-reset();
  @include main-font(16px, 14px, 1.61, 400);
  display: block;
  margin-top: 5px;
  letter-spacing: 0.02em;
  text-decoration: underline;
  color: $color-main;

  &:hover {
    color: $color-text;
  }
}
