.requisites {
  max-width: 500px;
  margin-top: 10px;

  @media (max-width: $extra-large) {
    margin-top: 0;
  }

  @media (max-width: $extra-small) {
    margin-top: 30px;
  }
}

.requisites__line {
  @include reset();
  @include main-font(16px, 14px, 1.67, 500);
  margin-bottom: 15px;
  letter-spacing: 0.02em;
  color: $color-text;

  @media (max-width: $extra-small) {
    margin-bottom: 6px;
  }

  span {
    font-weight: 400;
  }
}

.requisites__caption {
  @include main-font(16px, 14px, 1.67, 500);
  margin-top: 60px;
  letter-spacing: 0.02em;
  color: $color-text;

  @media (max-width: $extra-large) {
    margin-top: 36px;
  }
}
