.all-services {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.all-services__title {
  margin-bottom: 80px;

  @media (max-width: $small) {
    margin-bottom: 50px;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 20px;
  }
}

.all-services__list {
  @include list-reset();
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border-left: 1px solid $color-main;
  border-right: 1px solid $color-main;

  @media (max-width: $medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: $small) {
    grid-template-columns: 1fr;
    border-top: 1px solid $color-main;
  }

  &::before,
  &::after {
    @include pseudo(1px, 100%);
    top: 0;
    left: 33.3%;
    background: $color-main;

    @media (max-width: $medium) {
      left: 50%;
    }

    @media (max-width: $small) {
      display: none;
    }
  }

  &::after {
    left: 66.6%;

    @media (max-width: $medium) {
      display: none;
    }
  }
}

.all-services__item {
  position: relative;
  padding: 60px 20px 67px 60px;

  @media (max-width: $extra-large) {
    padding: 60px 20px 67px;
  }

  @media (max-width: $small) {
    padding: 25px 50px 57px 25px;
    border-bottom: 1px solid $color-main;
  }

  &--banner {
    display: flex;
    align-items: center;
    grid-column: span 2;
    padding: 60px 60px 67px;
    z-index: 1;
    background: $white;
    border-bottom: 1px solid $color-main;

    @media (max-width: $extra-large) {
      padding: 60px 20px 67px;
    }

    @media (max-width: $medium) {
      grid-column: auto;
      z-index: 0;
      background: transparent;
      border-bottom: none;
    }

    @media (max-width: $small) {
      padding: 38px 40px;
      border-bottom: 1px solid $color-main;
    }
  }

  &:nth-child(3n-2)::after {
    @include pseudo(100vw, 1px);
    background: $color-main;
    bottom: 0;
    left: calc((100vw - 1520px) / -2);

    @media (max-width: 1760px) {
      left: -120px;
    }

    @media (max-width: $extra-large) {
      left: -40px;
    }

    @media (max-width: $medium) {
      display: none;
    }
  }

  &:nth-child(2n-1)::after {
    @media (max-width: $medium) {
      @include pseudo(100vw, 1px);
      display: block;
      background: $color-main;
      bottom: 0;
      left: -40px;
    }

    @media (max-width: $small) {
      display: none;
    }
  }

  &:first-child::before {
    @include pseudo(100vw, 1px);
    background: $color-main;
    top: 0;
    left: calc((100vw - 1520px) / -2);

    @media (max-width: 1760px) {
      left: -120px;
    }

    @media (max-width: $extra-large) {
      left: -40px;
    }

    @media (max-width: $small) {
      display: none;
    }
  }
}

.all-services__item-link {
  @include link-reset();
  margin-bottom: 20px;

  @media (max-width: $extra-large) {
    width: 100%;
  }
}

.all-services__image-wrap {
  width: 308px;
  height: 216px;
  margin-bottom: 40px;
  border-radius: 125px;
  overflow: hidden;

  @media (max-width: $extra-large) {
    width: 100%;
    height: 180px;
  }

  @media (max-width: $small) {
    width: 70%;
  }

  @media (max-width: $extra-small) {
    height: 120px;
    margin-bottom: 20px;
  }
}

.all-services__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: $main-transition;

  .all-services__item-link:hover & {
    transform: scale(1.2);
  }
}

.all-services__item-name {
  display: inline;
  max-width: 300px;
  border-bottom: 1px solid transparent;
  transition: $main-transition;

  .all-services__item-link:hover & {
    color: $color-main;
    border-bottom: 1px solid $color-main;
  }
}

.all-services__sublist {
  @include list-reset();
}

.all-services__subitem {
  &:not(:last-child) {
    margin-bottom: 14px;
  }
}

.all-services__sublink {
  @include link-reset();
  @include main-font(18px, 16px, 1.25, 400);
  border-bottom: 1px solid transparent;
  color: $color-text--light;

  &:hover {
    color: $color-main;
    border-bottom: 1px solid $color-main;
  }
}

.all-services__item-text {
  @include reset();
  @include main-font(18px, 14px, 1.3, 400);
  width: 95%;
  color: $color-text--light;

  @media (max-width: $extra-small) {
    width: 100%;
    line-height: 1.4;
  }
}

.all-services__item-period {
  @include reset();
  @include main-font(14px, 12px, 1.25, 400);
  margin-top: 14px;
  color: #9f9f9f;

  span {
    color: $color-text--light;
  }
}

.all-services__price {
  @include reset();
  @include main-font(20px, 16px, 1.2, 500);
  position: absolute;
  bottom: 20px;
  right: 20px;
  color: $color-main;
}

.all-services__banner {
  width: 100%;
}

.all-services__banner-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;

  @media (max-width: $medium) {
    display: flex;
    flex-direction: column-reverse;
    gap: 20px;
  }

  @media (max-width: $small) {
    align-items: start;
  }
}

.all-services__banner-info {
  @media (max-width: $small) {
    width: 100%;
  }
}

.all-services__banner-name {
  max-width: 325px;
  margin-bottom: 30px;

  @media (max-width: $extra-small) {
    margin-bottom: 17px;
  }

  span {
    width: 100%;
    font-weight: 600;
    color: $color-main;
    white-space: nowrap;
  }
}

.all-services__banner-text {
  max-width: 325px;
  margin-bottom: 20px;
}

.all-services__banner-image-wrap {
  position: relative;
  width: 90%;
  padding-bottom: 90%;
  border-radius: 80px;
  overflow: hidden;

  @media (max-width: $medium) {
    margin-bottom: 30px;
  }

  @media (max-width: $small) {
    width: 70%;
    padding-bottom: 67%;
  }

  @media (max-width: $extra-small) {
    width: 100%;
    margin-bottom: 17px;
    border-radius: 50px;
  }

  &--desktop {
    @media (max-width: $medium) {
      display: none;
    }
  }

  &--mobile {
    display: none;

    @media (max-width: $medium) {
      display: block;
    }
  }
}

.all-services__banner-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
