.navigation {
  padding-bottom: 43px;

  @media (max-width: $extra-small) {
    padding-bottom: 18px;
  }
}

.navigation__list {
  @include list-reset();
  display: grid;
  grid-template-columns: auto 412px 412px;

  @media (max-width: 1500px) {
    grid-template-columns: auto 300px 300px;
  }

  @media (max-width: $medium) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas:
      "service topic"
      "contact contact";
  }

  @media (max-width: $small) {
    display: flex;
    flex-direction: column-reverse;
  }
}

.navigation__item {
  @media (max-width: $small) {
    border-bottom: 1px solid $color-main;
  }

  &:nth-child(1) {
    @media (max-width: $medium) {
      grid-area: contact;
    }

    @media (max-width: $small) {
      grid-area: auto;
    }
  }

  &:nth-child(2) {
    @media (max-width: $medium) {
      grid-area: service;
    }

    @media (max-width: $small) {
      grid-area: auto;
    }
  }

  &:nth-child(3) {
    @media (max-width: $medium) {
      grid-area: topic;
    }

    @media (max-width: $small) {
      grid-area: auto;
    }
  }
}

.navigation__item-name {
  @include reset();
  @include accent-font(20px, 18px, 1.1, 400);
  display: block;
  margin-top: 56px;
  margin-bottom: 17px;
  color: $white;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  transition: $main-transition;

  @media (max-width: $medium) {
    margin-top: 40px;
  }

  @media (max-width: $small) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0 19px;
    padding-right: 12px;
    text-transform: uppercase;
    cursor: pointer;
  }

  &--group {
    display: none;

    @media (max-width: $small) {
      display: flex;
    }
  }

  .navigation__sublist--group & {
    @media (max-width: $small) {
      display: none;
    }
  }
}

.navigation__item-icon {
  display: none;

  @media (max-width: $small) {
    display: block;
    fill: rgba($white, 0.4);
    transition: $main-transition;
  }

  .navigation__item-checkbox:checked ~ .navigation__item-name & {
    transform: rotate(-90deg);
  }
}

.navigation__sublist {
  @include list-reset();
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @media (max-width: $small) {
    position: relative;
    max-height: 0;
    z-index: -1;
    transition: $main-transition;
  }

  &--group {
    @media (max-width: $medium) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: $small) {
      grid-template-columns: 1fr;
    }
  }

  .navigation__item-checkbox:checked ~ &,
  .navigation__item-checkbox:checked ~ .navigation__sublist-group &,
  .navigation__item-checkbox:checked ~ .navigation__sublist--group & {
    max-height: 100vh;
    z-index: 0;
  }
}

.navigation__sublist-wrap {
  &--desktop {
    @media (max-width: $small) {
      display: none;
    }
  }
}

.navigation__sublist-item {
  @include main-font(16px, 14px, 1.7, 300);
  position: relative;
  padding-bottom: 8px;
  letter-spacing: 0.02em;
  color: $white;

  a {
    @include link-reset();
  }
}

.navigation__sublist-link {
  display: flex;
  align-items: center;
}

.navigation__sublist-icon {
  fill: $color-main;
  transition: $main-transition;

  .navigation__sublist-item:hover & {
    transform: rotate(90deg);
  }

  @media (max-width: $small) {
    display: none;
  }
}

.navigation__sublist-group {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-right: 100px;

  @media (max-width: $small) {
    grid-template-columns: 1fr;
    position: relative;
    max-height: 0;
    z-index: -1;
    transition: $main-transition;
  }

  .navigation__item-checkbox:checked ~ & {
    max-height: 100vh;
    z-index: 0;
  }
}

.navigation__detailed-list {
  @include list-reset();
  display: none;
  position: absolute;
  top: 33px;
  left: 70px;
  min-width: 250px;
  padding: 16px 28px 16px 16px;
  background: $color-main;
  border-radius: 10px;
  box-shadow: 0px 4px 15px rgba($black, 0.25);
  z-index: 1;

  @media (max-width: $medium) {
    top: 25px;
    min-width: 220px;
    padding: 12px 15px 12px 10px;
  }

  .navigation__sublist-item:hover & {
    display: block;

    @media (max-width: $small) {
      display: none;
    }
  }
}

.navigation__detailed-link {
  @include link-reset();
  @include main-font(16px, 14px, 1, 400);

  &:hover {
    color: $color-text;
  }
}
