// Viewport

$min: 320px;
$extra-small: 576px;
$small: 768px;
$medium: 1024px;
$large: 1200px;
$extra-large: 1440px;
$max: 1920px;

// Colors

$black: #000000;
$white: #ffffff;

$color-main: #7d9c9e;
$color-bg: #516e75;
$color-bg-dark: #3F555A;

$color-title: #161615;
$color-text: #222222;
$color-text--light: #4a4a4a;

// Transition

$main-transition: all 0.2s ease;
