.vacancy__list {
  @include list-reset();
  counter-reset: vacancy-list;
  border-bottom: 1px solid #bcbcbc;
}

.vacancy__item {
  border-top: 1px solid #bcbcbc;
}

.vacancy__item-headline {
  position: relative;
  display: grid;
  grid-template-columns: 191px calc((100% - 297px) * 0.75) calc((100% - 297px) * 0.25) 106px;
  align-items: center;
  padding: 39px 0 40px;
  cursor: pointer;

  @media (max-width: $extra-large) {
    grid-template-columns: 100px calc((100% - 180px) * 0.75) calc((100% - 180px) * 0.25) 80px;
  }

  @media (max-width: $large) {
    grid-template-columns: 100px calc((100% - 180px) * 0.7) calc((100% - 180px) * 0.3) 80px;
  }

  @media (max-width: $medium) {
    grid-template-columns: 100px calc((100% - 180px) * 0.6) calc((100% - 180px) * 0.4) 80px;
  }

  @media (max-width: $small) {
    grid-template-columns: 100px 1fr 60px;
    grid-template-areas:
      "counter name arrow"
      ". experience .";
    padding: 30px 0 30px;
  }

  @media (max-width: $extra-small) {
    grid-template-columns: 44px 1fr 60px;
    padding: 20px 0 21px;
  }

  &::before {
    @include accent-font(30px, 20px, 1.1, 400);
    content: "(0" counter(vacancy-list) ")";
    counter-increment: vacancy-list;
    align-self: flex-start;
    letter-spacing: 0.04em;
    color: $color-main;

    @media (max-width: $small) {
      grid-area: counter;
    }
  }
}

.vacancy__item-name {
  @include reset();
  @include accent-font(30px, 20px, 1.1, 400);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $color-text--light;

  @media (max-width: $small) {
    grid-area: name;
    margin-bottom: 10px;
  }

  .vacancy__item-checkbox:checked + .vacancy__item-headline & {
    color: $color-main;
  }
}

.vacancy__item-experience {
  @include reset();
  @include main-font(20px, 14px, 1.61, 400);
  color: #9f9f9f;

  @media (max-width: $small) {
    grid-area: experience;
  }

  span {
    font-weight: 500;
    color: $color-main;
  }
}

.vacancy__arrow {
  grid-column: -1 / -2;

  @media (max-width: $small) {
    grid-area: arrow;
  }
}

.vacancy__arrow-icon {
  display: block;
  margin-left: auto;
  opacity: 0;
  transform: translate(-50%);
  fill: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $medium) {
    opacity: 1;
  }

  @media (max-width: $small) {
    width: 36px;
    height: auto;
    transform: translate(0);
    margin-right: 20px;
  }

  @media (max-width: $extra-small) {
    margin-right: 0;
  }

  .vacancy__item-headline:hover .vacancy__arrow & {
    opacity: 1;
    transform: translate(0);
  }

  .vacancy__item-checkbox:checked + .vacancy__item-headline & {
    opacity: 1;
    transform: translate(20%) rotate(90deg);
  }
}

.vacancy__item-info {
  max-height: 0;
  overflow: hidden;
  transition: $main-transition;
  color: transparent;

  .vacancy__item-checkbox:checked ~ & {
    max-height: 300vh;
    padding-top: 10px;
    padding-bottom: 42px;
    color: $color-text;

    @media (max-width: $extra-small) {
      padding-bottom: 60px;
    }
  }
}

.vacancy__item-info-title {
  @include reset();
  @include accent-font(30px, 20px, 1.1, 400);
  margin-bottom: 21px;
  color: $color-title;
}

.vacancy__item-info ul {
  @include list-reset();

  &:not(:last-child) {
    margin-bottom: 42px;

    @media (max-width: $extra-small) {
      margin-bottom: 46px;
    }
  }
}

.vacancy__item-info li {
  @include main-font(16px, 14px, 1.61, 400);
  position: relative;
  letter-spacing: 0.02em;
  padding-left: 26px;

  @media (max-width: $small) {
    padding-left: 24px;
    line-height: 1.45;
    letter-spacing: normal;
  }

  &:not(:last-child) {
    margin-bottom: 11px;
  }

  &::before {
    @include pseudo(12px, 12px);
    top: 6px;
    left: 0;
    border: 1px solid $color-main;
    border-radius: 50%;

    @media (max-width: $small) {
      @include pseudo(10px, 10px);
    }
  }
}
