.score {
  margin: 180px 0;
  border-top: 1px solid $color-main;
  border-bottom: 1px solid $color-main;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.score__content {
  display: grid;
  grid-template-columns: calc(50% - 162px) 1fr 140px;
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    grid-template-columns: calc(50% - 162px) 1fr 100px;
    margin-right: -40px;
  }

  @media (max-width: $medium) {
    grid-template-columns: 1fr;
    margin: 0 -40px;
  }

  @media (max-width: $small) {
    margin: 0 -20px;
  }

  @media (max-width: 374px) {
    margin: 0 -10px;
  }
}

.score__info {
  align-self: center;

  @media (max-width: $medium) {
    padding: 50px 35px 44px 40px;
    border-bottom: 1px solid $color-main;
  }

  @media (max-width: $small) {
    padding: 50px 20px 44px;
  }

  @media (max-width: 374px) {
    padding: 50px 10px 44px;
  }
}

.score__title {
  margin-bottom: 24px;

  @media (max-width: $small) {
    margin-bottom: 18px;
  }
}

.score__text {
  @include main-font(20px, 14px, 1.4, 400);
  max-width: 90%;

  @media (max-width: $medium) {
    max-width: 700px;
  }
}

.score__list {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1px;
  background: $color-main;
  border-left: 1px solid $color-main;
  border-right: 1px solid $color-main;

  @media (max-width: $medium) {
    border: none;
    border-bottom: 1px solid $color-main;
  }
}

.score__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 0;
  background: $white;

  @media (max-width: $medium) {
    padding: 40px 0;
  }

  @media (max-width: $small) {
    padding: 36px 0 33px;
  }
}

.score__image {
  max-width: 58%;
  max-height: 100px;
  margin-bottom: 10px;
}

.score__rating {
  margin-top: auto;
  margin-bottom: 10px;

  @media (max-width: $small) {
    margin-bottom: 7px;
  }
}

.score__link {
  @include link-reset();
  @include main-font(20px, 16px, 1.61, 400);
  position: relative;
  letter-spacing: 0.02em;
  color: $color-text;

  &::after {
    @include pseudo(100%, 1px);
    bottom: 5px;
    left: 0;
    width: 0;
    background: $color-main;
    transition: $main-transition;
  }

  &:hover {
    color: $color-main;
  }

  &:hover::after {
    width: 100%;
  }
}

.score__general-link {
  @include link-reset();
  display: flex;
  align-items: center;
  writing-mode: tb-rl;
  transform: rotate(180deg);

  @media (max-width: $medium) {
    justify-content: center;
    writing-mode: lr-tb;
    transform: none;
  }

  span {
    position: relative;
    padding-top: 80px;

    @media (max-width: $medium) {
      display: flex;
      align-items: center;
      padding: 40px 0;
    }

    @media (max-width: $small) {
      padding: 24px 0;
    }
  }

  &:hover {
    background: $color-main;

    span {
      color: $white;
    }
  }
}

.score__general-icon-arrow {
  margin-top: 30px;
  fill: $color-main;
  transform: rotate(90deg);
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 37px;
    height: auto;
    margin-top: 20px;
  }

  @media (max-width: $medium) {
    width: 32px;
    margin-top: 0;
    margin-left: 14px;
    transform: rotate(0);
  }

  .score__general-link:hover & {
    fill: $white;
    transform: rotate(90deg) translateX(20px);

    @media (max-width: $medium) {
      transform: translateX(20px);
    }
  }
}

.score__general-icon-ellipse {
  margin-top: -32px;
  fill: transparent;
  stroke: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 51px;
    height: auto;
    margin-top: -20px;
  }

  @media (max-width: $medium) {
    width: 45px;
    height: auto;
    margin-top: 0;
    margin-left: -27px;
    transform: rotate(90deg);
  }

  .score__general-link:hover & {
    stroke: $white;
    transform: rotate(-60deg) translate(10px, -8px);

    @media (max-width: $medium) {
      transform: rotate(30deg);
    }
  }
}
