.rating {
  display: flex;
  align-items: center;

  @media (max-width: 1600px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.rating__stars {
  position: relative;
  width: 128px;
  height: 24px;
  background: url("../../img/icons/icon-stars-empty.svg") 0 0 / cover no-repeat;

  @media (max-width: $small) {
    width: 90px;
    height: 17px;
  }
}

.rating__stars-fill {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: url("../../img/icons/icon-stars-filled.svg") 0 0 / cover no-repeat;
}

.rating__date {
  @include main-font(14px, 14px, 16px, 400);
  margin-left: 18px;
  letter-spacing: 0.02em;
  color: #9f9f9f;

  @media (max-width: 1600px) {
    margin-top: 8px;
    margin-left: 0;
  }
}
