.ask {
  position: relative;
  height: 554px;
  margin: 180px 0;
  background: url("../../img/bg-ask.png");
  border-top: 1px solid $color-main;
  border-bottom: 1px solid $color-main;

  @media (max-width: $large) {
    height: 500px;
  }

  @media (max-width: $medium) {
    height: 450px;
    margin: 120px 0;
  }

  @media (max-width: $small) {
    height: auto;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.ask__content {
  height: 100%;

  @media (max-width: $small) {
    display: flex;
    flex-direction: column-reverse;
    margin: 0 -20px;
  }

  @media (max-width: 374px) {
    margin: 0 -10px;
  }
}

.ask__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: calc(50% - 62px);
  background-position: 0% 50%;
  background-size: cover;

  @media (max-width: $medium) {
    left: calc(50% + 26px);
  }

  @media (max-width: $small) {
    position: static;
    height: 380px;
    background-position: 0% 0%;
    background-size: cover;
  }

  @media (max-width: $extra-small) {
    height: 267px;
  }
}

.ask__content-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: calc(50% - 62px);
  height: 100%;
  border-right: 1px solid $color-main;

  @media (max-width: $medium) {
    width: calc(50% + 26px);
  }

  @media (max-width: $small) {
    width: 100%;
    height: auto;
    padding: 60px 20px;
    border-right: none;
    border-bottom: 1px solid $color-main;
  }

  @media (max-width: 374px) {
    padding: 60px 10px;
  }
}

.ask__title {
  max-width: 90%;
  margin-bottom: 30px;

  @media (max-width: $small) {
    max-width: 500px;
    margin-bottom: 21px;
  }
}
