.social__list {
  @include list-reset();
  display: flex;

  .menu__social & {
    @media (max-width: $large) {
      display: grid;
      grid-template-columns: repeat(2, 40px);
      gap: 15px;
    }

    @media (max-width: $medium) {
      display: flex;
      gap: 0;
    }
  }
}

.social__item {
  &:not(:last-child) {
    margin-right: 20px;

    .faq & {
      @media (max-width: $extra-small) {
        margin-right: 15px;
      }
    }
  }
}

.social__link {
  @include link-reset();
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background: $color-main;
  border-radius: 50%;

  &:hover {
    background: $color-text;
  }
}

.social__link-icon {
  fill: $white;
  transition: $main-transition;
}
