.question__text {
  @include reset();
  @include accent-font(22px, 18px, 1.1, 400);
  margin-bottom: 17px;
  text-transform: uppercase;
  color: $white;

  @media (max-width: $small) {
    display: none;
  }

  .menu__question & {
    @media (max-width: $medium) {
      @include accent-font(16px, 16px, 18px, 400);
      margin-bottom: 22px;
      letter-spacing: 0.025em;
    }

    @media (max-width: $small) {
      display: block;
    }
  }
}

.question__list {
  @include list-reset();
  display: flex;
  flex-wrap: wrap;

  &--mobile {
    display: none;

    @media (max-width: $small) {
      display: flex;
    }
  }
}

.question__item {
  margin-bottom: 10px;

  &:not(:last-child) {
    margin-right: 8px;
  }
}

.question__link {
  @include link-reset();
  @include main-font(13px, 13px, 1.4, 400);
  display: flex;
  align-items: center;
  padding: 12px 20px;
  background: $color-main;
  border-radius: 4px;
  color: $white;

  &:hover {
    background: $color-text;
  }
}

.question__icon {
  margin-right: 10px;

  .question__link--whatsapp & {
    fill: #25d366;
  }

  .question__link--telegram & {
    fill: #0a89ff;
  }

  .question__link--phone &,
  .question__link--mail & {
    fill: $white;
  }

  .question__link:hover & {
    fill: $white;
  }
}
