.gallery {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.gallery__headline {
  @media (max-width: $extra-small) {
    margin-right: -20px;
  }

  @media (max-width: 374px) {
    margin-right: -10px;
  }
}

.gallery__container {
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    margin-right: -40px;
  }

  @media (max-width: $small) {
    margin-right: -20px;
  }

  @media (max-width: 374px) {
    margin-right: -10px;
  }
}

.gallery__list {
  @include list-reset();
}

.gallery__image-wrap {
  position: relative;
  width: 100%;
  padding-bottom: 68%;
}

.gallery__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
