.rate {
  display: flex;
  align-items: center;
}

.rate__title {
  @include reset();
  @include main-font(14px, 14px, 1, 400);
  margin-right: 13px;
  letter-spacing: 0.02em;
  color: #828282;

  @media (max-width: $extra-small) {
    margin-right: 5px;
  }
}

.rate__label {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  &:not(:first-child) {
    margin-right: 6px;
  }
}

.rate__label-icon {
  fill: transparent;
  transition: all 0.3s ease;

  .rate__input:hover ~ .rate__label &,
  .rate__input:checked ~ .rate__label & {
    fill: $color-main;
  }
}

.rate__mark {
  display: flex;
  flex-direction: row-reverse;
  margin-right: 5px;
}
