.title {
  @include reset();
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $color-title;

  &--h1 {
    @include accent-font(150px, 44px, 1.1, 400);
    letter-spacing: 0.085em;
  }

  &--h2 {
    @include accent-font(80px, 40px, 1.1, 400);
  }

  &--h3 {
    @include accent-font(60px, 34px, 1.1, 400);
  }

  &--h4 {
    @include accent-font(50px, 27px, 1.1, 400);
  }

  &--h5 {
    @include accent-font(30px, 20px, 1.1, 400);
  }
}
