.contacts {
  margin: 180px 0;
  border-top: 1px solid $color-main;
  border-bottom: 1px solid $color-main;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.contacts__content {
  display: grid;
  grid-template-columns: calc(50% - 162px) 1fr 140px;
  height: 100%;
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    grid-template-columns: calc(50% - 162px) 1fr 100px;
    margin-right: -40px;
  }

  @media (max-width: $large) {
    grid-template-columns: 1fr;
    margin: 0 -40px;
  }

  @media (max-width: $small) {
    margin: 0 -20px;
  }

  @media (max-width: 374px) {
    margin: 0 -10px;
  }
}

.contacts__info {
  padding: 103px 40px 70px 0;

  @media (max-width: $extra-large) {
    padding: 80px 40px 70px 0;
  }

  @media (max-width: $large) {
    padding: 40px;
  }

  @media (max-width: $small) {
    padding: 68px 20px 22px;
  }

  @media (max-width: $extra-small) {
    padding: 40px 20px 35px;
  }

  @media (max-width: 374px) {
    padding: 40px 10px 22px;
  }
}

.contacts__title {
  margin-bottom: 50px;

  @media (max-width: $extra-small) {
    margin-bottom: 30px;
  }
}

.contacts__list {
  @include list-reset();
}

.contacts__item {
  display: grid;
  grid-template-columns: 55px auto;

  &:not(:last-child) {
    margin-bottom: 25px;
  }
}

.contacts__item-title {
  @include reset();
  @include accent-font(24px, 20px, 1.1, 400);
  margin-bottom: 10px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $color-title;

  @media (max-width: $extra-small) {
    letter-spacing: 0.02em;
  }
}

.contacts__item-text {
  @include reset();
  @include link-reset();
  @include main-font(16px, 14px, 1.67, 400);
  display: block;
  letter-spacing: 0.02em;
  color: $color-text;

  @media (max-width: $extra-small) {
    letter-spacing: normal;
  }

  & + & {
    @media (max-width: $extra-small) {
      margin-top: 8px;
    }
  }
}

a.contacts__item-text:hover {
  color: $color-main;
}

.contacts__location {
  padding: 58px 70px 42px;
  border-left: 1px solid $color-main;
  border-right: 1px solid $color-main;

  @media (max-width: $extra-large) {
    padding: 58px 40px 42px;
  }

  @media (max-width: $large) {
    padding: 40px;
    border-top: 1px solid $color-main;
    border-bottom: 1px solid $color-main;
    border-left: none;
    border-right: none;
  }

  @media (max-width: $small) {
    padding: 40px 20px 63px;
  }

  @media (max-width: 374px) {
    padding: 20px 10px;
  }
}

.contacts__map {
  max-width: 664px;
  margin-bottom: 40px;
}

.contacts__map-image-wrap {
  position: relative;
  width: 100%;
  padding-bottom: 58%;
  border-radius: 75px;
  overflow: hidden;

  @media (max-width: $extra-small) {
    border-radius: 45px;
  }
}

.contacts__map-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contacts__way {
  max-width: 664px;
}

.contacts__way-title {
  @include reset();
  @include accent-font(24px, 20px, 1.1, 400);
  margin-bottom: 16px;
  text-transform: uppercase;
  color: $color-title;
  letter-spacing: 0.04em;

  @media (max-width: $extra-small) {
    letter-spacing: 0.02em;
  }
}

.contacts__way-list {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;

  @media (max-width: $extra-small) {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

.contacts__way-point {
  @include reset();
  @include main-font(16px, 14px, 1.6, 500);
  margin-bottom: 2px;
  letter-spacing: 0.02em;
}

.contacts__way-case {
  @include reset();
  @include main-font(16px, 14px, 1.6, 400);
  margin-bottom: 2px;
  letter-spacing: 0.04em;
}

.contacts__general-link {
  @include link-reset();
  display: flex;
  align-items: center;
  writing-mode: tb-rl;
  transform: rotate(180deg);

  @media (max-width: $medium) {
    justify-content: center;
    writing-mode: lr-tb;
    transform: none;
    border-right: none;
  }

  span {
    position: relative;
    padding-top: 80px;

    @media (max-width: $medium) {
      display: flex;
      align-items: center;
      padding: 40px 0;
    }

    @media (max-width: $small) {
      padding: 24px 0;
    }
  }

  &:hover {
    background: $color-main;

    span {
      color: $white;
    }
  }
}

.contacts__general-icon-arrow {
  margin-top: 30px;
  fill: $color-main;
  transform: rotate(90deg);
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 37px;
    height: auto;
    margin-top: 20px;
  }

  @media (max-width: $medium) {
    width: 32px;
    margin-top: 0;
    margin-left: 14px;
    transform: rotate(0);
  }

  .contacts__general-link:hover & {
    fill: $white;
    transform: rotate(90deg) translateX(20px);

    @media (max-width: $medium) {
      transform: translateX(20px);
    }
  }
}

.contacts__general-icon-ellipse {
  margin-top: -32px;
  fill: transparent;
  stroke: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 51px;
    height: auto;
    margin-top: -20px;
  }

  @media (max-width: $medium) {
    width: 45px;
    height: auto;
    margin-top: 0;
    margin-left: -27px;
    transform: rotate(90deg);
  }

  .contacts__general-link:hover & {
    stroke: $white;
    transform: rotate(-60deg) translate(10px, -8px);

    @media (max-width: $medium) {
      transform: rotate(30deg);
    }
  }
}
