.popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
  z-index: 50;
  max-height: calc(100vh - 80px);
  overflow-y: auto;
  padding: 60px;
  margin: 0 auto;
  background: #ffffff;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.13);

  &.show {
    display: block;
  }

  &--small {
    width: 555px;
    max-width: 90%;

    @media (max-width: $extra-small) {
      padding: 50px 20px 30px;
    }
  }

  &--medium {
    width: 774px;
    max-width: 90%;

    @media (max-width: $extra-small) {
      padding: 50px 20px 30px;
    }
  }

  &--wide {
    width: 970px;
    max-width: 90%;

    @media (max-width: $extra-small) {
      padding: 50px 20px 30px;
    }
  }

  &::-webkit-scrollbar-track {
    background-color: #e0e0e0;
  }

  &::-webkit-scrollbar {
    width: 5px;
    background-color: #e0e0e0;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $color-main;
  }
}

.popup__button-close {
  @include button-reset();
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 31px;
  right: 27px;
  width: 25px;
  height: 25px;
  cursor: pointer;
  z-index: 1;

  @media (max-width: $extra-small) {
    top: 15px;
    right: 16px;
  }
}

.popup__button-close-icon {
  stroke: $color-text--light;
  transition: all 0.3s ease;

  .popup__button-close:hover & {
    stroke: $color-main;
  }
}

.privacy-popup .title {
  text-transform: none;
  letter-spacing: normal;
}

.privacy-popup .title--h4 {
  margin-bottom: 30px;
}

.privacy-popup .title--h5 {
  margin: 40px 0 20px;
}

.privacy-popup .text {
  margin: 16px 0;
}

.popup__title {
  margin-bottom: 28px;
}

.popup__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;

  @media (max-width: 450px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.popup__rate {
  @media (max-width: 450px) {
    margin-bottom: 10px;
  }
}

.popup__button-wrap {
  display: flex;
  align-items: center;
  margin-top: 48px;

  @media (max-width: $extra-small) {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 35px;
  }
}

.popup__button {
  width: 177px;
  height: 46px;
  flex-shrink: 0;

  @media (max-width: 450px) {
    width: 100%;
  }
}

.popup__caption {
  @include reset();
  @include main-font(12px, 10px, 1.6, 400);
  max-width: 290px;
  margin-left: 35px;
  color: #828282;

  @media (max-width: $extra-small) {
    margin-left: 0;
    margin-top: 10px;
  }
}

.popup__caption-link {
  @include link-reset();
  @include main-font(12px, 10px, 1.6, 400);
  color: $color-main;

  &:hover {
    color: $color-text;
  }
}
