.file-input__label {
  @include main-font(14px, 14px, 1, 400);
  display: flex;
  align-items: center;
  color: $color-main;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    color: $color-text;
  }
}

.file-input__label-icon {
  margin-left: 15px;
  fill: $color-main;
  transition: all 0.3s ease;

  @media (max-width: $extra-small) {
    width: 14px;
    height: auto;
    margin-left: 5px;
  }

  .file-input__label:hover & {
    fill: $color-text;
  }
}
