.headline {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
  gap: 0 40px;

  @media (max-width: $extra-small) {
    margin-bottom: 40px;
  }
}

.headline__name {
  max-width: 680px;

  span {
    @include main-font(20px, 14px, 1.6, 400);
    display: block;
    max-width: 650px;
    margin-top: 23px;
    letter-spacing: 0.02em;
    color: $color-text;
    text-transform: none;

    @media (max-width: $extra-small) {
      margin-top: 18px;
      line-height: 1.45;
    }
  }
}

.headline__link {
  @include link-reset();
  @include main-font(25px, 20px, 1.61, 400);
  align-self: flex-end;
  padding-bottom: 10px;
  letter-spacing: 0.02em;
  color: $color-main;

  &:hover {
    color: $color-text;
  }
}

.headline__buttons {
  position: relative;
  display: flex;

  @media (max-width: $extra-small) {
    display: none;
  }
}

.headline__button {
  @include button-reset();
  display: flex;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 35px;

  &--prev {
    margin-right: 24px;
  }
}

.headline__button-icon {
  fill: $color-main;
  transition: $main-transition;

  .headline__button--prev & {
    transform: rotate(180deg);
  }

  .headline__button:hover & {
    fill: $color-title;
  }
}

.headline__current {
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
  color: $color-main !important;

  @media (max-width: $large) {
    top: -25px;
  }

  @media (max-width: $medium) {
    top: -20px;
  }
}
