.text {
  @include reset();
  @include main-font(20px, 14px, 1.6, 400);
  letter-spacing: 0.02em;
  color: $color-text;

  &--small {
    @include main-font(16px, 14px, 1.67, 400);
    letter-spacing: normal;
  }

  &--big {
    @include main-font(20px, 18px, 1.6, 400);
  }

  &--pale {
    color: #9f9f9f;
  }
}
