.stocks-block {
  height: 651px;
  margin: 180px 0;
  background: url("../../img/bg-ask.png");
  border-top: 1px solid $color-main;
  border-bottom: 1px solid $color-main;

  @media (max-width: $medium) {
    height: auto;
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.stocks-block__content {
  display: grid;
  grid-template-columns: calc(50% - 162px) calc(50% + 22px) 140px;
  height: 100%;
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    grid-template-columns: calc(50% - 162px) calc(50% + 62px) 100px;
    margin-right: -40px;
  }

  @media (max-width: $medium) {
    display: block;
    margin: 0 -40px;
  }

  @media (max-width: $small) {
    margin: 0 -20px;
  }

  @media (max-width: 374px) {
    margin: 0 -10px;
  }
}

.stocks-block__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-right: 1px solid $color-main;

  @media (max-width: $medium) {
    padding: 51px 35px 58px 40px;
    border-bottom: 1px solid $color-main;
    border-right: none;
  }

  @media (max-width: $small) {
    padding: 51px 20px 37px;
  }

  @media (max-width: 374px) {
    padding: 51px 10px 37px;
  }
}

.stocks-block__title {
  margin-bottom: 24px;

  @media (max-width: $small) {
    margin-bottom: 16px;
  }
}

.stocks-block__text {
  @include reset();
  @include main-font(20px, 14px, 1.6, 400);
  letter-spacing: 0.02em;
  color: $color-text;
  max-width: 90%;

  @media (max-width: $medium) {
    max-width: 607px;
  }

  @media (max-width: $small) {
    line-height: 1.44;
  }
}

.stocks-block__slider {
  position: relative;
}

.stocks-block__container {
  height: 100%;
  background: $white;

  @media (max-width: $medium) {
    padding: 53px 0 40px;
  }
}

.stocks-block__list {
  @include list-reset();
}

.stocks-block__item {
  display: flex;
  align-items: center;
}

.stocks-block__item-wrap {
  width: 450px;
  margin-left: 160px;

  @media (max-width: 1600px) {
    margin: 0 auto;
  }

  @media (max-width: $small) {
    width: calc(100vw - 120px);
    max-width: 450px;
  }
}

.stocks-block__name {
  width: 90%;
  margin-bottom: 12px;

  @media (max-width: $extra-small) {
    margin-bottom: 18px;
  }

  span {
    width: 100%;
    font-weight: 600;
    color: $color-main;
    white-space: nowrap;
  }
}

.stocks-block__image-wrap {
  position: relative;
  width: 100%;
  margin-bottom: 22px;
  padding-bottom: 67%;
  border-radius: 80px;
  overflow: hidden;

  @media (max-width: $extra-small) {
    margin-bottom: 18px;
    border-radius: 50px;
  }
}

.stocks-block__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.stocks-block__text {
  margin-bottom: 20px;
}

.stocks-block__general-link {
  @include link-reset();
  display: flex;
  align-items: center;
  writing-mode: tb-rl;
  background: $white;
  transform: rotate(180deg);
  border-right: 1px solid $color-main;

  @media (max-width: $medium) {
    justify-content: center;
    writing-mode: lr-tb;
    transform: none;
    border-top: 1px solid $color-main;
    border-right: none;
  }

  span {
    position: relative;
    padding-top: 80px;

    @media (max-width: $medium) {
      display: flex;
      align-items: center;
      padding: 40px 0;
    }

    @media (max-width: $small) {
      padding: 24px 0;
    }
  }

  &:hover {
    background: $color-main;

    span {
      color: $white;
    }
  }
}

.stocks-block__general-icon-arrow {
  margin-top: 30px;
  fill: $color-main;
  transform: rotate(90deg);
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 37px;
    height: auto;
    margin-top: 20px;
  }

  @media (max-width: $medium) {
    width: 32px;
    margin-top: 0;
    margin-left: 14px;
    transform: rotate(0);
  }

  .stocks-block__general-link:hover & {
    fill: $white;
    transform: rotate(90deg) translateX(20px);

    @media (max-width: $medium) {
      transform: translateX(20px);
    }
  }
}

.stocks-block__general-icon-ellipse {
  margin-top: -32px;
  fill: transparent;
  stroke: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 51px;
    height: auto;
    margin-top: -20px;
  }

  @media (max-width: $medium) {
    width: 45px;
    height: auto;
    margin-top: 0;
    margin-left: -27px;
    transform: rotate(90deg);
  }

  .stocks-block__general-link:hover & {
    stroke: $white;
    transform: rotate(-60deg) translate(10px, -8px);

    @media (max-width: $medium) {
      transform: rotate(30deg);
    }
  }
}

.stocks-block__buttons {
  position: absolute;
  bottom: 61px;
  left: -69px;
  display: flex;
  z-index: 1;

  @media (max-width: $large) {
    bottom: 20px;
  }

  @media (max-width: $medium) {
    top: 20px;
    bottom: auto;
    left: auto;
    right: 20px;
  }
}

.stocks-block__button {
  @include button-reset();
  display: flex;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 35px;

  @media (max-width: $small) {
    width: 35px;
    height: 21px;
  }

  &--prev {
    margin-right: 24px;

    @media (max-width: $small) {
      margin-right: 15px;
    }
  }
}

.stocks-block__button-icon {
  fill: $color-main;
  transition: $main-transition;

  .stocks-block__button--prev & {
    transform: rotate(180deg);
  }

  .stocks-block__button:hover & {
    fill: $color-title;
  }
}
