.breadcrumbs {
  position: relative;
  padding: 30px 0 47px;
  z-index: 1;

  @media (max-width: $large) {
    padding: 30px 0;
  }

  @media (max-width: $small) {
    padding: 12px 0 10px;
  }
}

.breadcrumbs__list {
  @include list-reset();
}

.breadcrumbs__item {
  @include main-font(16px, 16px, 1.6, 400);
  display: inline-block;
  color: #9f9f9f;
  letter-spacing: 0.02em;
  transition: $main-transition;

  &:last-child {
    color: $color-text--light;
  }

  &:nth-last-child(2) {
    @media (max-width: $small) {
      padding-left: 15px;
      background: url("../../img/icons/icon-breadcrumbs.svg") 0 50% no-repeat;
    }
  }

  &:not(:nth-last-child(2)) {
    @media (max-width: $small) {
      display: none;
    }
  }

  &:not(:last-child)::after {
    @include main-font(14px, 16px, 1.4, 400);
    content: "/";
    margin: 0 4px 0 12px;
    color: #c5c5c5;

    @media (max-width: $small) {
      display: none;
    }
  }

  &:not(:last-child):hover {
    color: $color-main;
  }

  &:nth-last-child(2):hover {
    @media (max-width: $small) {
      color: #9f9f9f;
    }
  }
}

.breadcrumbs__link {
  @include link-reset();
}
