.menu {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding-top: 150px;
  z-index: 11;
  background: linear-gradient(
    90deg,
    $color-bg calc((1520px * 0.22) + calc((100% - 1520px) / 2)),
    $color-bg-dark calc((1520px * 0.22) + calc((100% - 1520px) / 2))
  );
  transition: all 0.3s ease;

  @media (max-width: 1760px) {
    background: linear-gradient(
      90deg,
      $color-bg calc(((100% - 240px) * 0.22) + 120px),
      $color-bg-dark calc(((100% - 240px) * 0.22) + 120px)
    );
  }

  @media (max-width: $extra-large) {
    background: linear-gradient(
      90deg,
      $color-bg calc(((100% - 80px) * 0.22) + 40px),
      $color-bg-dark calc(((100% - 80px) * 0.22) + 40px)
    );
  }

  @media (max-width: $medium) {
    display: block;
    top: 111px;
    left: -200vw;
    height: calc(100vh - 111px);
    max-width: 400px;
    padding-top: 0;
    background: $color-bg;
  }

  @media (max-width: $small) {
    top: 79px;
    height: calc(100vh - 79px);
  }

  @media (max-width: $extra-small) {
    top: 60px;
    height: calc(100vh - 60px);
  }

  &.show {
    display: block;

    @media (max-width: $medium) {
      left: 0;
    }
  }
}

.menu__wrapper {
  height: 100%;
  padding-top: 50px;
  overflow-y: auto;

  @media (max-width: $medium) {
    padding-top: 24px;
  }

  &::-webkit-scrollbar-track {
    background-color: rgba($white, 0.5);
  }

  &::-webkit-scrollbar {
    width: 8px;
    background-color: rgba($white, 0.5);

    @media (max-width: $medium) {
      width: 5px;
    }
  }

  &::-webkit-scrollbar-thumb {
    background-color: $color-main;
  }
}

.menu__content {
  display: grid;
  grid-template-columns: 21.5% 1fr;

  @media (max-width: $medium) {
    grid-template-columns: 1fr;
    margin: 0 -30px;
    padding-bottom: 90px;
  }

  @media (max-width: $small) {
    margin: 0 -10px;
  }

  @media (max-width: 374px) {
    margin: 0;
  }
}

.menu__title {
  @include reset();
  @include accent-font(50px, 30px, 1.1, 400);
  margin-bottom: 60px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $white;

  @media (max-width: $medium) {
    display: none;
  }
}

.menu__question {
  width: 85%;
  margin-bottom: 73px;

  @media (max-width: $medium) {
    margin-bottom: 18px;
    padding-left: 10px;
  }
}

.menu__social {
  display: none;

  @media (max-width: $extra-large) {
    display: block;
  }

  @media (max-width: $medium) {
    padding-left: 14px;
  }
}

.menu__close-services-button {
  display: none;

  @media (max-width: $medium) {
    @include button-reset();
    @include main-font(18px, 18px, 22px, 400);
    display: flex;
    align-items: center;
    padding-left: 12px;
    color: rgba($white, 0.9);
  }
}

.menu__close-services-button-icon {
  margin-right: 10px;
  transform: rotate(180deg);
  fill: $white;
}
