.not-found {
  margin: 45px 0 65px;
}

.not-found__content {
  display: flex;

  @media (max-width: $medium) {
    flex-direction: column;
  }
}

.not-found__info {
  flex-shrink: 0;

  @media (max-width: $medium) {
    margin: 0 auto;
    text-align: center;
  }
}

.not-found__title {
  margin-bottom: 16px;
  letter-spacing: 0.04em;
}

.not-found__text {
  max-width: 440px;
  margin-bottom: 30px;

  @media (max-width: $extra-small) {
    width: 100%;
  }
}

.not-found__animation {
  margin: -90px auto 0;

  @media (max-width: $large) {
    margin: -70px auto 0;
  }

  @media (max-width: $medium) {
    margin: 0 auto;
  }
}

.not-found__animation-image {
  width: 620px;
  height: 620px;
  background: transparent;

  @media (max-width: $large) {
    width: 450px;
    height: 450px;
  }

  @media (max-width: $extra-small) {
    width: 100%;
    height: auto;
  }
}
