.promo {
  position: relative;
  height: 100vh;
  background-image: url("../../img/bg-promo.png");
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;

  &::after {
    @include pseudo(100%, 100%);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(5, 5, 5, 0.55);
  }
}

.promo__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.promo__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.promo__content {
  position: relative;
  display: flex;
  align-items: flex-end;
  padding-bottom: 60px;
  height: 100%;
  z-index: 1;

  @media (max-width: $extra-small) {
    padding-bottom: 33px;
  }
}

.promo__title {
  color: $white;

  @media (max-width: $extra-small) {
    letter-spacing: 0.04em;
  }

  @media (max-width: 374px) {
    font-size: 39px;
  }

  span {
    display: flex;
    align-items: center;

    @media (max-width: $extra-small) {
      justify-content: space-between;
      align-items: baseline;
    }
  }
}

.promo__button-play {
  @include button-reset();
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 124px;
  height: 124px;
  margin-left: 23px;
  text-align: center;

  @media (max-width: $medium) {
    width: 90px;
    height: 90px;
    margin-right: 23px;
  }

  @media (max-width: 374px) {
    margin-right: 10px;
  }
}

.promo__button-play-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: $white;
  transition: $main-transition;

  @media (max-width: $medium) {
    width: 30px;
    height: 30px;
  }

  .promo__button-play:hover & {
    fill: $color-main;
  }
}

.promo__button-play-text-icon {
  fill: $white;
  transition: $main-transition;
  animation: rotation 10s linear infinite;

  @media (max-width: $medium) {
    width: 90px;
    height: 90px;
  }

  .promo__button-play:hover & {
    fill: $color-main;
    animation-play-state: paused;
  }
}

.promo__scroll {
  @include link-reset();
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 70px;
  height: 60px;
  background: rgba($color-main, 0.25);
  backdrop-filter: blur(25px);
  border-radius: 10px 10px 0px 0px;

  @media (max-width: $large) {
    width: 50px;
    height: 50px;
  }

  @media (max-width: $small) {
    display: none;
  }
}

.promo__scroll-icon {
  fill: $color-main;
  animation: scroll 2s ease infinite;
  transition: $main-transition;

  @media (max-width: $large) {
    width: 16px;
    height: 27px;
  }

  .promo__scroll:hover & {
    fill: $white;
  }
}

.promo__social-list {
  @include list-reset();
  position: absolute;
  bottom: 94px;
  right: 0;

  @media (max-width: $extra-large) {
    bottom: 85px;
  }

  @media (max-width: $large) {
    bottom: 78px;
  }

  @media (max-width: $medium) {
    display: none;
  }
}

.promo__social-item {
  &:not(:last-child) {
    margin-bottom: 13px;

    @media (max-width: $large) {
      margin-bottom: 6px;
    }
  }
}

.promo__social-link-icon {
  fill: $white;
  transition: $main-transition;

  .promo__social-link:hover & {
    fill: $color-main;
  }
}
