.input-group {
  display: flex;

  @media (max-width: $extra-small) {
    flex-direction: column;
  }

  .feedback & {
    @media (max-width: $extra-large) {
      flex-direction: column;
    }

    @media (max-width: $large) {
      flex-direction: row;
    }

    @media (max-width: $extra-small) {
      flex-direction: column;
    }
  }
}
