.prices {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }

  .top-banner + & {
    margin-top: -75px;
  }
}

.prices__container {
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    margin-right: -40px;
  }

  @media (max-width: $small) {
    margin-right: -20px;
  }

  @media (max-width: 374px) {
    margin-right: -10px;
  }
}

.prices__list {
  @include list-reset();
  display: flex;
}

.prices__item {
  @include main-font(20px, 16px, 1.61, 400);
  flex-shrink: 0;
  flex-grow: 0;
  width: auto !important;
  letter-spacing: 0.02em;
  color: $color-text;
  cursor: pointer;
}

.prices__item-name {
  @include reset();
  padding: 0 35px 11px;
  border-bottom: 1px solid $color-main;
  transition: $main-transition;

  @media (max-width: $extra-small) {
    padding: 0 7px 14px;
  }

  .swiper-slide-active & {
    padding: 0 35px 8px;
    border-bottom: 4px solid $color-main;
    color: $color-main;
    text-shadow: 0 0 0.9px $color-main, 0 0 0.9px $color-main;

    @media (max-width: $extra-small) {
      padding: 0 7px 12px;
    }
  }

  &:hover {
    color: $color-main;
  }
}
