.insta {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.insta__headline {
  @media (max-width: $medium) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.insta__headline .headline__link {
  @media (max-width: $medium) {
    align-self: flex-start;
    margin-top: 16px;
  }
}

.insta__list {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;

  @media (max-width: $large) {
    gap: 20px;
  }

  @media (max-width: $medium) {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 1fr;
    gap: 30px;
  }

  @media (max-width: $small) {
    gap: 16px;
  }
}

.insta__image-wrap {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #e0e0e0;
}

.insta__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
