.top-block {
  margin: 180px 0;
  border-top: 1px solid $color-main;
  border-bottom: 1px solid $color-main;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: -102px 0 180px;

    @media (max-width: $medium) {
      margin: -102px 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: -102px 0 90px;
    }
  }

  &--reverse {
    .breadcrumbs + & {
      @media (max-width: $large) {
        margin: 0 0 180px;
        border-top: none;
      }

      @media (max-width: $medium) {
        margin: 0 0 120px;
      }

      @media (max-width: $extra-small) {
        margin: 0 0 90px;
      }
    }
  }
}

.top-block__content {
  display: grid;
  grid-template-columns: 40.5% auto;
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    margin-right: -40px;
  }

  @media (max-width: $large) {
    display: flex;
    flex-direction: column;
    margin: 0 -40px;
  }

  @media (max-width: $small) {
    margin: 0 -20px;
  }

  @media (max-width: 374px) {
    margin: 0 -10px;
  }

  .top-block--reverse & {
    @media (max-width: $large) {
      flex-direction: column-reverse;
      margin: 0;
    }

    @media (max-width: $small) {
      margin: 0;
    }

    @media (max-width: 374px) {
      margin: 0;
    }
  }
}

.top-block__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 102px 80px 102px 0;
  border-right: 1px solid $color-main;

  @media (max-width: $extra-large) {
    padding: 102px 40px 102px 0;
  }

  @media (max-width: $large) {
    padding: 102px 40px 60px;
    border-bottom: 1px solid $color-main;
    border-right: none;
  }

  @media (max-width: $small) {
    padding: 102px 20px 40px;
  }

  @media (max-width: 374px) {
    padding: 102px 10px 20px;
  }

  .top-block--reverse & {
    @media (max-width: $large) {
      padding: 60px 0;
      border: none;
    }

    @media (max-width: $medium) {
      padding: 40px 0;
      border: none;
    }

    @media (max-width: $small) {
      padding: 25px 0 40px;
    }
  }
}

.top-block__title {
  margin-bottom: 20px;

  @media (max-width: $small) {
    margin-bottom: 12px;
  }
}

.top-block__subtitle {
  margin-bottom: 48px;
  text-transform: none;
  letter-spacing: normal;

  @media (max-width: $small) {
    margin-bottom: 20px;
  }
}

.top-block__text {
  margin-bottom: 40px;

  @media (max-width: $large) {
    margin-bottom: 20px;
  }
}

.top-block__image-wrap {
  width: 100%;
  height: 100%;
  border-radius: 0 100px;
  overflow: hidden;

  @media (max-width: $extra-small) {
    border-radius: 0 50px;
  }
}

.top-block__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
