.input {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  @media (max-width: $extra-small) {
    margin-bottom: 10px;
  }

  .input-group & {
    flex-grow: 1;
  }

  .input-group &:first-child {
    margin-right: 30px;

    @media (max-width: $extra-small) {
      margin-right: 0;
    }

    .feedback & {
      @media (max-width: 1600px) {
        margin-right: 20px;
      }

      @media (max-width: $extra-large) {
        margin-right: 0;
      }

      @media (max-width: $large) {
        margin-right: 30px;
      }

      @media (max-width: $extra-small) {
        margin-right: 0;
      }
    }
  }
}

.input__label {
  @include main-font(16px, 14px, 1.61, 400);
  order: -1;
  opacity: 0.7;
  margin-bottom: 7px;
  letter-spacing: 0.02em;
  color: #828282;
  transition: $main-transition;

  @media (max-width: $extra-small) {
    margin-bottom: 4px;
  }

  &--star {
    color: #ec4839;
  }

  .input__field:not(.input__field--invalid):focus ~ & {
    color: $color-main;
    opacity: 1;
  }

  .input__field--invalid ~ & {
    color: #eb5757;
  }
}

.input__field {
  @include input-reset();
  @include main-font(16px, 16px, 1.75, 400);
  width: 100%;
  max-width: 100%;
  padding: 16px 14px 14px 16px;
  border: 1px solid #e0e0e0;
  border-radius: 2px;
  color: $color-text;
  background-color: $white;
  caret-color: $color-main;

  @media (max-width: $extra-small) {
    padding: 12px 15px;
  }

  &::placeholder {
    @include main-font(16px, 16px, 1.75, 400);
    color: #999999;
  }

  &:not(.input__field--invalid):hover,
  &:not(.input__field--invalid):focus {
    border: 1px solid $color-main;
  }

  &--invalid {
    border: 1px solid #eb5757;
    background: $white url("../img/icons/icon-invalid.svg") no-repeat calc(100% - 16px) 50%;
  }
}

textarea.input__field {
  padding-bottom: 26px;

  @media (max-width: $extra-small) {
    padding-bottom: 37px;
  }
}

.input__note {
  @include main-font(14px, 12px, 1.2, 400);
  display: none;
  margin-top: 2px;
  color: #ff7171;

  .input__field--invalid ~ &--invalid {
    display: block;
  }
}
