.menu-right {
  padding-left: 100px;

  @media (max-width: $medium) {
    position: fixed;
    top: 111px;
    left: -400vw;
    width: 100%;
    max-width: 400px;
    height: calc(100vh - 111px);
    padding: 0 10px;
    z-index: 12;
    background: $color-bg;
    transition: all 0.3s ease;
    overflow-y: auto;
  }

  @media (max-width: $small) {
    top: 79px;
    height: calc(100vh - 79px);
  }

  @media (max-width: $extra-small) {
    top: 60px;
    height: calc(100vh - 60px);
  }

  .services-menu & {
    @media (max-width: $medium) {
      left: 0;
    }
  }

  &::-webkit-scrollbar-track {
    background-color: rgba($white, 0.5);
  }

  &::-webkit-scrollbar {
    width: 8px;
    background-color: rgba($white, 0.5);

    @media (max-width: $medium) {
      width: 5px;
    }
  }

  &::-webkit-scrollbar-thumb {
    background-color: $color-main;
  }
}

.menu-right__list {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: auto;
  gap: 60px 70px;
  margin-bottom: 220px;

  @media (max-width: 1600px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media (max-width: $large) {
    grid-template-columns: repeat(2, 1fr);
    gap: 50px 20px;
  }

  @media (max-width: $medium) {
    margin-top: 30px;
  }

  .services-menu & {
    @media (max-width: $medium) {
      display: block;
      margin-bottom: 90px;
      overflow-y: auto;
    }
  }

  &::-webkit-scrollbar-track {
    background-color: rgba($white, 0.5);
  }

  &::-webkit-scrollbar {
    width: 8px;
    background-color: rgba($white, 0.5);

    @media (max-width: $medium) {
      width: 5px;
    }
  }

  &::-webkit-scrollbar-thumb {
    background-color: $color-main;
  }
}

.menu-right__item {
  @media (max-width: $medium) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 6px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
  }
}

.menu-right__sublist-title {
  @include reset();
  @include accent-font(24px, 24px, 1.28, 400);
  margin-bottom: 30px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;

  @media (max-width: $large) {
    width: 80%;
    margin-bottom: 22px;
  }

  @media (max-width: $medium) {
    @include main-font(20px, 20px, 1.1, 400);
    width: 100%;
    margin-bottom: 0;
    padding: 22px 0 22px 10px;
    letter-spacing: normal;
    text-transform: none;
  }

  &:hover {
    @media (max-width: $medium) {
      color: $color-main;
    }
  }
}

.menu-right__sublist {
  @include list-reset();

  .services-menu & {
    overflow-y: auto;

    @media (max-width: $medium) {
      position: fixed;
      top: 111px;
      left: -200vw;
      width: 100%;
      max-width: 400px;
      height: calc(100vh - 111px);
      padding: 0 10px 90px 10px;
      z-index: 12;
      background: $color-bg;
      transition: all 0.3s ease;

      @media (max-width: $small) {
        top: 79px;
        height: calc(100vh - 79px);
      }

      @media (max-width: $extra-small) {
        top: 60px;
        height: calc(100vh - 60px);
      }

      &.show {
        left: 0;
      }
    }

    &::-webkit-scrollbar-track {
      background-color: rgba($white, 0.5);
    }

    &::-webkit-scrollbar {
      width: 8px;
      background-color: rgba($white, 0.5);

      @media (max-width: $medium) {
        width: 5px;
      }
    }

    &::-webkit-scrollbar-thumb {
      background-color: $color-main;
    }
  }
}

.menu-right__subitem {
  width: 84%;

  @media (max-width: 1700px) {
    width: 100%;
  }

  @media (max-width: $medium) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
  }

  &:not(:last-child) {
    margin-bottom: 14px;

    @media (max-width: $large) {
      margin-bottom: 10px;
    }

    @media (max-width: $medium) {
      margin-bottom: 0;
    }
  }

  &--button  {
    display: none;

    @media (max-width: $medium) {
      display: block;
      margin-bottom: 10px !important;
      border-bottom: none;
    }
  }
}

.menu__close-extra-button {
  @include button-reset();
  @include main-font(18px, 18px, 22px, 400);
  display: flex;
  align-items: center;
  padding-left: 12px;
  color: rgba($white, 0.9);
}

.menu__close-extra-button-icon {
  margin-right: 10px;
  transform: rotate(180deg);
  fill: $white;
}

.menu-right__sublink {
  @include link-reset();
  @include main-font(16px, 16px, 20px, 400);
  display: inline;
  position: relative;
  color: #cdcdcd;
  border-bottom: 1px solid transparent;
  transition: $main-transition;

  @media (max-width: $medium) {
    display: block;
    width: 75%;
    margin-bottom: 0;
    padding: 22px 0 21px 10px;
    letter-spacing: normal;
    text-transform: none;
  }

  &:hover {
    color: $white;
    border-bottom: 1px solid $white;

    @media (max-width: $medium) {
      border-bottom: none;
    }
  }
}

.menu-right__more-link {
  display: none;

  @media (max-width: $medium) {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 38px;
    height: 39px;

    &::before {
      @include pseudo(1px, 100%);
      top: 0;
      left: -11px;
      background: rgba(255, 255, 255, 0.12);
    }
  }
}

.menu-right__more-link-icon {
  fill: $white;
  transition: $main-transition;

  .menu-right__more-link:hover & {
    fill: $color-main;
  }
}
