.logo__image {
  @media (max-width: $small) {
    width: 66px;
    height: auto;
  }

  &--light {
    display: none;

    .header--main & {
      display: block;
    }

    .header.scroll & {
      display: none;
    }

    .open-menu & {
      display: block;
    }
  }

  &--dark {
    .header--main & {
      display: none;
    }

    .header.scroll & {
      display: block;
    }

    .open-menu & {
      display: none;
    }
  }
}
