.works {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.works__container {
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    margin-right: -40px;
  }

  @media (max-width: $small) {
    margin-right: -20px;
  }

  @media (max-width: 374px) {
    margin-right: -10px;
  }
}

.works__list {
  @include list-reset();
}

.works__item {
  &:not(.swiper-slide-active) {
    opacity: 0.2;
  }
}

.works__gallery {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
  margin-bottom: 62px;

  @media (max-width: $max) {
    gap: 60px;
  }

  @media (max-width: 1600px) {
    gap: 30px;
  }

  @media (max-width: $medium) {
    grid-template-columns: 1fr;
  }

  @media (max-width: $extra-small) {
    gap: 25px;
    margin-bottom: 37px;
  }
}

.works__gallery-period {
  display: block;
  margin-bottom: 22px;

  @media (max-width: $extra-small) {
    margin-bottom: 8px;
  }
}

.works__gallery-image-wrap {
  position: relative;
  width: calc(100% - 32px);
  margin-left: 32px;
  padding-bottom: 50%;
  border-radius: 0px 0px 50px 0px;
  overflow: hidden;

  @media (max-width: $extra-small) {
    margin-left: 29px;
    padding-bottom: 48%;
    border-radius: 0px 0px 40px 0px;
  }
}

.works__gallery-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.works__info {
  display: none;
  width: 43%;
  margin-left: 32px;

  @media (max-width: $medium) {
    width: 100%;
  }

  .swiper-slide-active & {
    display: block;
  }
}

.works__description {
  margin-bottom: 25px;

  @media (max-width: $extra-small) {
    width: calc(100% + 25px);
    margin-right: -25px;
    margin-bottom: 20px;
    line-height: 1.44;
  }
}

.works__artists {
  @include list-reset();
}

.works__artist {
  @media (max-width: $extra-small) {
    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }

  a {
    @include link-reset();
    position: relative;
    color: $color-main;

    &:hover {
      color: $color-text;
    }

    &::after {
      @include pseudo(100%, 1px);
      position: absolute;
      bottom: 5px;
      left: 0;
      background-color: $color-main;

      @media (max-width: $extra-small) {
        display: none;
      }
    }

    &:hover::after {
      background-color: $color-text;
    }
  }
}
