.video-block {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }

  .top-block + & {
    @media (max-width: $small) {
      margin-top: -30px;
    }
  }
}

.video-block__content {
  display: grid;
  grid-template-columns: 56% 44%;
  align-items: center;

  @media (max-width: $large) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: $medium) {
    display: flex;
    flex-direction: column;
  }
}

.video-block__media {
  width: 100%;
}

.video-block__image-wrap {
  position: relative;
  width: 100%;
  max-width: 855px;
  padding-bottom: 64.5%;
  border-radius: 0 100px 0 0;
  overflow: hidden;
}

.video-block__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.video-block__button-play {
  @include button-reset();
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 124px;
  height: 124px;
  text-align: center;

  @media (max-width: $medium) {
    width: 90px;
    height: 90px;
  }
}

.video-block__button-play-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: $white;
  transition: $main-transition;

  @media (max-width: $medium) {
    width: 30px;
    height: 30px;
  }

  .video-block__button-play:hover & {
    fill: $color-main;
  }
}

.video-block__button-play-text-icon {
  fill: $white;
  transition: $main-transition;
  animation: rotation 10s linear infinite;

  @media (max-width: $medium) {
    width: 90px;
    height: 90px;
  }

  .video-block__button-play:hover & {
    fill: $color-main;
    animation-play-state: paused;
  }
}

.video-block__info-content {
  width: 88%;
  padding-left: 112px;

  @media (max-width: 1600px) {
    width: 100%;
    padding-left: 80px;
  }

  @media (max-width: $large) {
    padding-left: 60px;
  }

  @media (max-width: $medium) {
    width: 85%;
    padding-left: 0;
    padding-top: 60px;
  }

  @media (max-width: $extra-small) {
    width: 100%;
    padding-top: 40px;
  }
}

.video-block__title {
  margin-bottom: 25px;
}
