.faq {
  margin: 180px 0;
  border-top: 1px solid $color-main;
  border-bottom: 1px solid $color-main;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.faq__content {
  display: grid;
  grid-template-columns: calc(50% - 162px) 1fr 140px;
  height: 100%;
  min-height: 746px;
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    grid-template-columns: calc(50% - 162px) 1fr 100px;
    min-height: 0;
    margin-right: -40px;
  }

  @media (max-width: $medium) {
    min-height: auto;
    grid-template-columns: 1fr;
    margin: 0 -40px;
  }

  @media (max-width: $small) {
    margin: 0 -20px;
  }

  @media (max-width: 374px) {
    margin: 0 -10px;
  }
}

.faq__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 40px 50px 78px 0;

  @media (max-width: $extra-large) {
    justify-content: flex-start;
    padding: 60px 30px 60px 0;
  }

  @media (max-width: $large) {
    padding: 40px 20px 50px 0;
  }

  @media (max-width: $medium) {
    max-width: 648px;
    padding: 40px;
  }

  @media (max-width: $small) {
    padding: 40px 20px 35px;
  }

  @media (max-width: 374px) {
    padding: 30px 10px;
  }
}

.faq__image-wrap {
  position: relative;
  width: 100%;
  margin-bottom: 28px;
  padding-bottom: 75%;
  border-radius: 300px;
  overflow: hidden;

  @media (max-width: $medium) {
    padding-bottom: 67%;
  }

  @media (max-width: $small) {
    margin-bottom: 20px;
  }
}

.faq__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.faq__video-link {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.faq__button-play {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 94px;
  height: 94px;
  border: 1px solid $white;
  border-radius: 50%;
  transition: $main-transition;

  @media (max-width: $extra-large) {
    width: 75px;
    height: 75px;
  }

  @media (max-width: $extra-small) {
    width: 47px;
    height: 47px;
  }

  .faq__video-link:hover & {
    background-color: $white;
  }
}

.faq__button-play-icon {
  margin-left: 2px;
  fill: $white;
  transition: $main-transition;

  @media (max-width: $extra-small) {
    width: 15px;
    height: 15px;
  }

  .faq__video-link:hover & {
    fill: $color-main;
  }
}

.faq__title {
  margin-bottom: 30px;

  @media (max-width: $large) {
    margin-bottom: 20px;
  }
}

.faq__footer {
  display: flex;
  align-items: center;

  @media (max-width: $large) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.faq__footer-text {
  @include reset();
  @include main-font(14px, 12px, 1.1, 400);
  max-width: 226px;
  margin-left: 40px;
  color: $color-text--light;

  @media (max-width: $large) {
    max-width: none;
    margin-left: 0;
    margin-top: 16px;
  }
}

.faq__list {
  @include list-reset();
  padding: 166px 149px 40px 67px;
  border-left: 1px solid $color-main;
  border-right: 1px solid $color-main;

  @media (max-width: 1800px) {
    padding: 166px 67px 40px;
  }

  @media (max-width: $extra-large) {
    padding: 100px 67px 60px;
  }

  @media (max-width: $large) {
    padding: 80px 40px 60px;
  }

  @media (max-width: $medium) {
    padding: 40px;
    border-top: 1px solid $color-main;
    border-bottom: 1px solid $color-main;
    border-left: none;
    border-right: none;
  }

  @media (max-width: $small) {
    padding: 43px 20px 22px;
  }

  @media (max-width: 374px) {
    padding: 30px 10px;
  }
}

.faq__item {
  position: relative;

  @media (max-width: $medium) {
    max-width: 648px;
  }
}

.faq__item-button-play {
  @include  button-reset();
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  width: 39px;
  height: 39px;
  border-radius: 50%;
  border: 1px solid $color-main;

  &:hover {
    background: $color-main;
  }
}

.faq__item-button-play-icon {
  margin-left: 2px;
  fill: $color-main;
  transition: $main-transition;

  .faq__item-button-play:hover & {
    fill: $white;
  }
}

.faq__item-name {
  @include accent-font(30px, 20px, 1.1, 400);
  display: block;
  position: relative;
  margin-right: 60px;
  padding-left: 37px;
  color: $color-title;
  transition: $main-transition;
  cursor: pointer;

  @media (max-width: $extra-small) {
    padding-left: 20px;
  }

  &::before {
    @include pseudo(17px, 17px);
    top: 9px;
    left: 0px;
    border-radius: 50%;
    border: 1px solid $color-main;

    @media (max-width: $extra-small) {
      @include pseudo(10px, 10px);
    }
  }

  &:hover {
    color: $color-main;
  }

  .faq__item-checkbox:checked ~ & {
    color: $color-main;
  }
}


.faq__answer {
  @include main-font(16px, 14px, 1.67, 400);
  max-height: 0;
  width: 87%;
  margin-right: 60px;
  padding: 23px 0 23px 37px;
  letter-spacing: 0.02em;
  overflow: hidden;
  transition: $main-transition;
  color: transparent;

  @media (max-width: $extra-small) {
    width: 100%;
    padding: 19px 30px 12px 20px;
    letter-spacing: normal;
    line-height: 1.44;
  }

  .faq__item:last-child & {
    padding-bottom: 0;
  }

  p {
    @include reset();
  }

  .faq__item-checkbox:checked ~ & {
    max-height: 100vh;
    padding-bottom: 58px;
    color: $color-text;

    @media (max-width: $large) {
      padding-bottom: 23px;
    }

    @media (max-width: $extra-small) {
      padding-bottom: 29px;
    }
  }
}

.faq__general-link {
  @include link-reset();
  display: flex;
  align-items: center;
  writing-mode: tb-rl;
  transform: rotate(180deg);

  @media (max-width: $medium) {
    justify-content: center;
    writing-mode: lr-tb;
    transform: none;
    border-right: none;
  }

  span {
    position: relative;
    padding-top: 80px;

    @media (max-width: $medium) {
      display: flex;
      align-items: center;
      padding: 40px 0;
    }

    @media (max-width: $small) {
      padding: 24px 0;
    }
  }

  &:hover {
    background: $color-main;

    span {
      color: $white;
    }
  }
}

.faq__general-icon-arrow {
  margin-top: 30px;
  fill: $color-main;
  transform: rotate(90deg);
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 37px;
    height: auto;
    margin-top: 20px;
  }

  @media (max-width: $medium) {
    width: 32px;
    margin-top: 0;
    margin-left: 14px;
    transform: rotate(0);
  }

  .faq__general-link:hover & {
    fill: $white;
    transform: rotate(90deg) translateX(20px);

    @media (max-width: $medium) {
      transform: translateX(20px);
    }
  }
}

.faq__general-icon-ellipse {
  margin-top: -32px;
  fill: transparent;
  stroke: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 51px;
    height: auto;
    margin-top: -20px;
  }

  @media (max-width: $medium) {
    width: 45px;
    height: auto;
    margin-top: 0;
    margin-left: -27px;
    transform: rotate(90deg);
  }

  .faq__general-link:hover & {
    stroke: $white;
    transform: rotate(-60deg) translate(10px, -8px);

    @media (max-width: $medium) {
      transform: rotate(30deg);
    }
  }
}
