.feedback {
  margin: 180px 0;
  background: url("../../img/bg-ask.png");
  border-top: 1px solid $color-main;
  border-bottom: 1px solid $color-main;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.feedback__content {
  display: grid;
  grid-template-columns: calc(50% - 162px) 1fr 140px;
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    grid-template-columns: calc(50% - 162px) 1fr 100px;
    margin-right: -40px;
  }

  @media (max-width: $large) {
    grid-template-columns: 1fr;
    margin: 0 -40px;
  }

  @media (max-width: $small) {
    margin: 0 -20px;
  }

  @media (max-width: 374px) {
    margin: 0 -10px;
  }
}

.feedback__info {
  padding: 66px 107px 72px 0;

  @media (max-width: 1600px) {
    padding: 66px 50px 72px 0;
  }

  @media (max-width: $large) {
    padding: 40px;
  }

  @media (max-width: $small) {
    padding: 68px 20px 22px;
  }

  @media (max-width: 374px) {
    padding: 40px 10px 22px;
  }
}

.feedback__info-title {
  @include reset();
  @include accent-font(42px, 34px, 1.1, 400);
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  color: $color-title;

  @media (max-width: $extra-small) {
    margin-bottom: 10px;
  }
}

.feedback__info-text {
  @include reset();
  @include main-font(16px, 14px, 1.67, 400);
  max-width: 400px;
  letter-spacing: 0.02em;
  color: $color-text;

  @media (max-width: $extra-small) {
    letter-spacing: normal;
    line-height: 1.5;
  }
}

.feedback__form-content {
  max-width: 591px;
  margin-top: 30px;

  @media (max-width: $extra-small) {
    margin-top: 25px;
  }
}

.feedback__form-footer {
  display: flex;
  align-items: center;
  gap: 30px;
  margin-top: 30px;

  @media (max-width: $extra-small) {
    flex-direction: column;
    align-items: flex-start;
    gap: 11px;
    margin-top: 20px;
  }
}

.feedback__button {
  flex-shrink: 0;
  align-self: flex-start;
  width: 177px;

  @media (max-width: $extra-small) {
    width: 100%;
    max-width: 335px;
    height: 50px;
  }
}

.feedback__caption {
  @include reset();
  @include main-font(12px, 12px, 1.6, 400);
  max-width: 285px;
  color: #828282;

  @media (max-width: $extra-small) {
    max-width: 290px;
  }
}

.feedback__caption-link {
  @include link-reset();
  color: $color-main;
  text-decoration: underline;

  &:hover {
    color: $color-text;
  }
}

.feedback__tour {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 66px 58px 66px 70px;
  background: $white;
  border-left: 1px solid $color-main;
  border-right: 1px solid $color-main;

  @media (max-width: 1600px) {
    padding: 66px 50px;
  }

  @media (max-width: $large) {
    padding: 40px;
    border-top: 1px solid $color-main;
    border-bottom: 1px solid $color-main;
    border-left: none;
    border-right: none;
  }

  @media (max-width: $small) {
    padding: 40px 20px;
  }

  @media (max-width: 374px) {
    padding: 20px 10px;
  }
}

.feedback__tour-image-wrap {
  position: relative;
  width: 100%;
  padding-bottom: 63%;
  border-radius: 50px;
  overflow: hidden;
}

.feedback__tour-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.feedback__tour-text {
  @include reset();
  @include accent-font(30px, 20px, 1.1, 400);
  max-width: 425px;
  margin-top: 20px;
  color: $color-title;
}

.feedback__general-link {
  @include link-reset();
  display: flex;
  align-items: center;
  writing-mode: tb-rl;
  background: $white;
  transform: rotate(180deg);

  @media (max-width: $large) {
    justify-content: center;
    writing-mode: lr-tb;
    transform: none;
  }

  span {
    position: relative;
    padding-top: 80px;

    @media (max-width: $large) {
      display: flex;
      align-items: center;
      padding: 40px 0;
    }

    @media (max-width: $small) {
      padding: 24px 0;
    }
  }

  &:hover {
    background: $color-main;

    span {
      color: $white;
    }
  }
}

.feedback__general-icon-arrow {
  margin-top: 30px;
  fill: $color-main;
  transform: rotate(90deg);
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 37px;
    height: auto;
    margin-top: 20px;
  }

  @media (max-width: $large) {
    width: 32px;
    margin-top: 0;
    margin-left: 14px;
    transform: rotate(0);
  }

  .feedback__general-link:hover & {
    fill: $white;
    transform: rotate(90deg) translateX(20px);

    @media (max-width: $large) {
      transform: translateX(20px);
    }
  }
}

.feedback__general-icon-ellipse {
  margin-top: -32px;
  fill: transparent;
  stroke: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: 51px;
    height: auto;
    margin-top: -20px;
  }

  @media (max-width: $large) {
    width: 45px;
    height: auto;
    margin-top: 0;
    margin-left: -27px;
    transform: rotate(90deg);
  }

  .feedback__general-link:hover & {
    stroke: $white;
    transform: rotate(-60deg) translate(10px, -8px);

    @media (max-width: $large) {
      transform: rotate(30deg);
    }
  }
}
