.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 30px 0;
  z-index: 12;
  transition: all 0.5s ease;
  background-color: $white;
  border-bottom: 1px solid #f2f2f2;

  @media (max-width: $medium) {
    padding: 16px 0;
  }

  @media (max-width: $extra-small) {
    padding: 8px 0;
  }

  &--main {
    background: transparent;
    border-bottom: none;
  }

  &.scroll {
    background-color: $white;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
  }

  .open-menu & {
    background-color: transparent;
    border-bottom: none;

    @media (max-width: $medium) {
      background-color: $color-bg;
    }
  }
}

.header__content {
  display: grid;
  grid-template-columns: 1fr 118px 1fr;
  gap: 20px;

  @media (max-width: $small) {
    grid-template-columns: 1fr 66px 1fr;
  }
}

.header__left {
  display: flex;
  align-items: center;
}

.header__social {
  display: none;

  .open-menu & {
    display: block;
    margin-left: 50px;

    @media (max-width: 1700px) {
      margin-left: 0;
    }

    @media (max-width: $extra-large) {
      display: none;
    }
  }
}

.header__list {
  @include list-reset();
  display: flex;
  align-items: center;

  @media (max-width: $small) {
    display: none;
  }

  .open-menu & {
    display: none;
  }
}

.header__item {
  &:not(:last-child) {
    margin-right: 26px;
  }

  &:nth-child(4) {
    @media (max-width: 1700px) {
      display: none;
    }
  }

  &:nth-child(2),
  &:nth-child(3) {
    @media (max-width: $large) {
      display: none;
    }
  }
}

.header__link {
  @include link-reset();
  @include main-font(18px, 18px, 1.2, 400);
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: $color-text;

  &:hover {
    color: $color-main;
  }

  .header--main & {
    color: $white;

    &:hover {
      color: $color-main;
    }
  }

  .header.scroll & {
    color: $color-text;

    &:hover {
      color: $color-main;
    }
  }
}

.header__right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.header__call-link {
  @include link-reset();
  @include main-font(16px, 16px, 1.2, 400);
  display: block;
  margin-bottom: 8px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: $color-text;

  @media (max-width: $small) {
    margin-bottom: 0;
    padding: 0 10px;
  }

  &:hover {
    color: $color-main;
  }

  .header--main & {
    color: $white;

    &:hover {
      color: $black;
    }
  }

  .header.scroll & {
    color: $color-text;

    &:hover {
      color: $color-main;
    }
  }

  .open-menu & {
    color: $white;

    &:hover {
      color: $color-text;
    }
  }

  span {
    @media (max-width: $small) {
      display: none;
    }
  }
}

.header__call-link-icon {
  display: none;
  transition: $main-transition;

  @media (max-width: $small) {
    display: block;
    fill: $color-text;
  }

  .header__call-link:hover & {
    fill: $color-main;
  }

  .header--main & {
    fill: $white;
  }

  .header--main .header__call-link:hover & {
    fill: $color-main;
  }

  .open-menu & {
    fill: $white;
  }

  .header.scroll & {
    fill: $color-text;
  }

  .header.scroll .header__call-link:hover & {
    fill: $color-main;
  }
}

.header__call-button {
  @include button-reset();
  @include main-font(14px, 14px, 1.2, 500);
  color: $color-main;

  @media (max-width: $small) {
    display: none;
  }

  &:hover {
    color: $color-text;
  }

  .header--main & {
    color: $color-main;

    &:hover {
      color: $white;
    }
  }

  .open-menu & {
    &:hover {
      color: $white;
    }
  }

  .header.scroll & {
    &:hover {
      color: $color-text;
    }
  }
}

.header__location {
  margin-left: 51px;

  @media (max-width: $large) {
    display: none;
  }
}

.header__location-current {
  @include main-font(16px, 16px, 1.2, 400);
  display: block;
  margin-bottom: 8px;
  letter-spacing: 0.02em;
  color: $color-text;

  .header--main & {
    color: $white;
  }

  .open-menu & {
    color: $white;
  }

  .header.scroll & {
    color: $color-text;
  }
}

.header__location-change {
  @include button-reset();
  @include main-font(14px, 14px, 1.2, 500);
  color: $color-main;

  &:hover {
    color: $color-text;
  }

  .header--main & {
    &:hover {
      color: $white;
    }
  }

  .open-menu & {
    &:hover {
      color: $white;
    }
  }

  .header.scroll & {
    &:hover {
      color: $color-text;
    }
  }
}

.header__appointment {
  margin-left: 78px;

  @media (max-width: 1850px) {
    margin-left: 48px;
  }

  @media (max-width: 1700px) {
    display: none;
  }
}

.header__appointment-button {
  padding: 14px 24px;
  color: $color-text;
  border-color: $color-text;

  &:hover {
    color: $color-main;
    border-color: $color-main;
    background: transparent;
  }

  .header--main & {
    color: $white;
    border-color: $white;

    &:hover {
      color: $color-main;
      border-color: $color-main;
      background: $color-text;
    }
  }

  .open-menu & {
    color: $white;
    border-color: $white;

    &:hover {
      color: $color-main;
      border-color: $color-main;
      background: $color-text;
    }
  }

  .header.scroll & {
    color: $color-text;
    border-color: $color-text;

    &:hover {
      color: $color-main;
      border-color: $color-main;
      background: transparent;
    }
  }
}
