.services {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.services__title {
  margin-bottom: 85px;

  @media (max-width: $extra-large) {
    margin-bottom: 60px;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 40px;
  }
}

.services__list {
  @include list-reset();
  counter-reset: services-list;
  border-bottom: 1px solid #bcbcbc;
}

.services__link {
  @include link-reset();
  @include accent-font(70px, 24px, 1.1, 400);
  position: relative;
  display: grid;
  grid-template-columns: 130px calc((100% - 236px) * 0.7) calc((100% - 236px) * 0.3) 106px;
  align-items: center;
  width: 100%;
  padding: 63px 0 64px;
  border-top: 1px solid #bcbcbc;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: $color-text--light;

  @media (max-width: $extra-large) {
    grid-template-columns: 100px calc((100% - 180px) * 0.7) calc((100% - 180px) * 0.3) 80px;
    padding: 55px 0;
  }

  @media (max-width: $large) {
    grid-template-columns: 100px calc((100% - 180px) * 0.6) calc((100% - 180px) * 0.4) 80px;
    padding: 50px 0;
  }

  @media (max-width: $medium) {
    grid-template-columns: 100px 1fr 80px;
  }

  @media (max-width: $small) {
    grid-template-columns: 80px 1fr 80px;
    padding: 40px 0;
  }

  @media (max-width: $extra-small) {
    grid-template-columns: 70px 1fr 50px;
    padding: 19px 0 20px;
  }

  @media (max-width: 374px) {
    grid-template-columns: 50px 1fr 50px;
  }

  &:hover {
    color: $color-main;
  }

  &::before {
    @include accent-font(30px, 16px, 1.1, 400);
    width: 130px;
    content: "(0" counter(services-list) ")";
    counter-increment: services-list;
    align-self: flex-start;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  }
}

.services__card {
  position: absolute;
  top: 50%;
  right: 106px;
  transform: translateY(-50%) translateX(-100%);
  width: calc((100% - 236px) * 0.3);
  opacity: 0;
  z-index: 1;
  transition: all 0.5s ease;

  @media (max-width: $extra-large) {
    width: calc((100% - 180px) * 0.3);
  }

  @media (max-width: $large) {
    width: calc((100% - 180px) * 0.4);
  }

  @media (max-width: $medium) {
    display: none;
  }

  .services__link:hover & {
    opacity: 1;
    transform: translateY(-50%) translateX(0);
  }
}

.services__card-content {
  padding-bottom: 75%;
}

.services__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.services__price {
  @include main-font(21px, 16px, 1.2, 600);
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  width: 213px;
  text-transform: none;
}

.services__arrow {
  grid-column: -1 / -2;
}

.services__arrow-icon {
  display: block;
  margin-left: auto;
  opacity: 0;
  transform: translate(-50%);
  fill: $color-main;
  transition: all 0.5s ease;

  @media (max-width: $medium) {
    opacity: 1;
  }

  @media (max-width: $small) {
    width: 36px;
    height: auto;
  }

  @media (max-width: $extra-small) {
    transform: translate(0);
  }

  .services__link:hover .services__arrow & {
    opacity: 1;
    transform: translate(0);
  }
}
