.menu-left__header {
  display: none;

  @media (max-width: $medium) {
    display: flex;
    flex-wrap: wrap;
    padding-left: 10px;
  }
}

.menu-left__call {
  display: none;

  @media (max-width: $small) {
    display: block;
    margin-right: 41px;
  }

  @media (max-width: $extra-small) {
    margin-right: 20px;
  }
}

.menu-left__call-link {
  @include link-reset();
  @include main-font(16px, 16px, 1.2, 400);
  display: block;
  margin-bottom: 8px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: $white;
}

.menu-left__call-button {
  @include button-reset();
  @include main-font(14px, 14px, 1.2, 500);
  color: $color-main;
}

.menu-left__location-change {
  @include button-reset();
  @include main-font(16px, 16px, 1.2, 400);
  border-bottom: 1px dashed $white;
  color: $white;

  &:hover {
    color: $color-text;
    border-bottom: 1px dashed $color-text;
  }
}

.menu-left__list {
  @include list-reset();
  margin-bottom: 120px;

  @media (max-width: $medium) {
    margin-top: 70px;
    margin-bottom: 70px;
  }

  @media (max-width: $medium) {
    margin-top: 43px;
  }
}

.menu-left__item {
  position: relative;

  @media (max-width: $medium) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
  }

  &--mobile {
    display: none;

    @media (max-width: $medium) {
      display: flex;
    }
  }

  &:not(:last-child) {
    margin-bottom: 30px;

    @media (max-width: $large) {
      margin-bottom: 22px;
    }

    @media (max-width: $medium) {
      margin-bottom: 0;
    }
  }
}

.menu-left__link {
  @include link-reset();
  @include accent-font(20px, 20px, 1.1, 400);
  position: relative;
  display: inline;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $white;

  @media (max-width: $medium) {
    @include main-font(20px, 20px, 1.1, 400);
    display: block;
    width: 100%;
    padding: 17px 0 16px 10px;
    letter-spacing: normal;
    text-transform: none;
  }

  &:hover {
    border-bottom: 1px solid $white;

    @media (max-width: $medium) {
      color: $color-main;
      border-bottom: none;
    }
  }
}

.menu-left__more-link {
  display: none;

  @media (max-width: $medium) {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 38px;
    height: 39px;

    &::before {
      @include pseudo(1px, 100%);
      top: 0;
      left: -11px;
      background: rgba(255, 255, 255, 0.12);
    }
  }
}

.menu-left__more-link-icon {
  fill: $white;
  transition: $main-transition;

  .menu-left__more-link:hover & {
    fill: $color-main;
  }
}
