.reviews {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.reviews__name {
  max-width: 700px;
  margin-bottom: 60px;
}

.reviews__list {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 68px 58px;

  @media (max-width: $extra-large) {
    gap: 45px 35px;
  }

  @media (max-width: $large) {
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;
  }

  @media (max-width: $medium) {
    gap: 45px;
  }

  @media (max-width: $small) {
    grid-template-columns: 1fr;
    gap: 60px;
  }
}

.reviews__footer {
  display: flex;
  justify-content: center;
  margin-top: 70px;

  @media (max-width: $small) {
    margin-top: 40px;
  }
}

.reviews__footer-link {
  @include button-reset();
  @include accent-font(20px, 18px, 1.1, 400);
  position: relative;
  letter-spacing: 0.155em;
  text-transform: uppercase;
  color: $color-main;

  &:hover {
    color: $color-text;
  }

  &:after {
    @include pseudo(100%, 1px);
    bottom: 2px;
    left: 0;
    background: $color-main;
    transition: $main-transition;
  }

  &:hover:after {
    background: $color-text;
  }
}
