.blog {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.blog__headline {
  margin-bottom: 90px;

  @media (max-width: $extra-small) {
    margin-bottom: 40px;
  }
}

.blog__container {
  .blog--slider & {
    @media (max-width: $medium) {
      margin-right: -40px;
    }

    @media (max-width: $small) {
      margin-right: -20px;
    }

    @media (max-width: 374px) {
      margin-right: -10px;
    }
  }
}

.blog__list {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 100px 94px;

  @media (max-width: 1600px) {
    gap: 80px 50px;
  }

  @media (max-width: $large) {
    gap: 60px 20px;
  }

  @media (max-width: $medium) {
    grid-template-rows: auto;
    gap: 70px 50px;
  }

  @media (max-width: $small) {
    gap: 60px 20px;
  }

  @media (max-width: $extra-small) {
    display: flex;
    flex-direction: column;
  }

  @media (max-width: $extra-small) {
    gap: 56px;
  }

  .blog--slider & {
    display: flex;
    gap: 0;

    @media (max-width: $extra-small) {
      flex-direction: row;
    }
  }
}

.blog__item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 35px;

  @media (max-width: 1600px) {
    gap: 25px;
  }

  @media (max-width: $medium) {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  @media (max-width: $extra-small) {
    gap: 15px;
  }
}

.blog__item-cover {
  margin-right: 5px;

  @media (max-width: $medium) {
    margin-right: 0;
  }
}

.blog__item-image-wrap {
  position: relative;
  width: 100%;
  padding-bottom: 103%;
  border-radius: 80px;
  overflow: hidden;

  @media (max-width: $large) {
    border-radius: 50px;
  }

  @media (max-width: $extra-small) {
    padding-bottom: 61%;
    border-radius: 20px;
  }
}

.blog__item-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.blog__info {
  display: flex;
  flex-direction: column;
  margin: 5px 0;

  @media (max-width: $medium) {
    flex-grow: 1;
  }
}

.blog__item-date {
  @include reset();
  @include main-font(16px, 14px, 1.66, 400);
  margin-bottom: 17px;
  letter-spacing: 0.02em;
  color: $color-main;

  @media (max-width: $medium) {
    margin-bottom: 10px;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 7px;
  }
}

.blog__item-name {
  @include reset();
  @include accent-font(30px, 20px, 1.1, 400);
  margin-bottom: 20px;
  color: $color-title;

  @media (max-width: $medium) {
    margin-bottom: 10px;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 8px;
  }
}

.blog__item-text {
  @include reset();
  @include main-font(16px, 14px, 1.67, 400);
  margin-bottom: 18px;
  letter-spacing: 0.02em;
  color: $color-text;

  @media (max-width: $extra-small) {
    line-height: 1.5;
    letter-spacing: normal;
  }
}

.blog__item-link {
  width: 165px;
  align-self: flex-start;
  margin-top: auto;

  @media (max-width: $extra-small) {
    width: 130px;
  }
}

.blog__footer {
  display: flex;
  justify-content: center;
  margin-top: 113px;

  @media (max-width: $large) {
    margin-top: 70px;
  }

  @media (max-width: $small) {
    margin-top: 40px;
  }
}

.blog__footer-link {
  @include button-reset();
  @include accent-font(20px, 18px, 1.1, 400);
  position: relative;
  letter-spacing: 0.155em;
  text-transform: uppercase;
  color: $color-main;

  &:hover {
    color: $color-text;
  }

  &:after {
    @include pseudo(100%, 1px);
    bottom: 2px;
    left: 0;
    background: $color-main;
    transition: $main-transition;
  }

  &:hover:after {
    background: $color-text;
  }
}
