.reviews-block {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.reviews-block__container {
  margin-right: -200px;

  @media (max-width: $max - 1) {
    margin-right: calc((100vw - 1520px) / -2);
  }

  @media (max-width: 1760px) {
    margin-right: -120px;
  }

  @media (max-width: $extra-large) {
    margin-right: -40px;
  }

  @media (max-width: $small) {
    margin-right: -20px;
  }

  @media (max-width: 374px) {
    margin-right: -10px;
  }
}

.reviews-block__list {
  @include list-reset();
}

.reviews-block__item {
  display: flex;
  flex-direction: column;
  height: auto;
}

.reviews-block__name {
  margin-bottom: 20px;

  @media (max-width: $extra-small) {
    margin-bottom: 15px;
  }
}

.reviews-block__info {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-left: 32px;

  .popup & {
    padding-left: 0;
  }
}

.reviews-block__info-wrap {
  position: relative;
  margin-bottom: 20px;

  @media (max-width: $extra-small) {
    margin-bottom: 14px;
  }

  .popup & {
    margin-bottom: 32px;

    @media (max-width: $extra-small) {
      margin-bottom: 22px;
    }
  }
}

.reviews-block__image-wrap {
  position: relative;
  width: 100%;
  padding-bottom: 55%;
  border-radius: 0 0 50px 0;
  overflow: hidden;

  @media (max-width: $extra-small) {
    border-radius: 0 0 41px 0;
  }
}

.reviews-block__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top center;
}

.reviews-block__video-link {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.reviews-block__button-play {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border: 1px solid $white;
  border-radius: 50%;
  transition: $main-transition;

  .reviews-block__video-link:hover & {
    background-color: $white;
  }
}

.reviews-block__button-play-icon {
  margin-left: 2px;
  fill: $white;
  transition: $main-transition;

  .reviews-block__video-link:hover & {
    fill: $color-main;
  }
}

.reviews-block__link {
  @include button-reset();
  @include main-font(14px, 13px, 1.14, 500);
  position: absolute;
  bottom: 34px;
  left: -66px;
  writing-mode: tb-rl;
  transform: rotate(-90deg);
  letter-spacing: 0.02em;
  color: $color-main;
  text-transform: uppercase;

  @media (max-width: $extra-small) {
    bottom: 31px;
    left: -64px;
  }

  &::after {
    @include pseudo(100%, 1px);
    background: $color-main;
    bottom: 0;
    left: 1px;
    transition: $main-transition;
  }

  &:hover {
    color: $color-text;
  }

  &:hover::after {
    background: $color-text;
  }
}

.reviews-block__text {
  width: 85%;
  margin-bottom: 10px;

  @media (max-width: $extra-small) {
    width: 100%;
    margin-bottom: 15px;
    line-height: 1.35;
  }

  .popup & {
    width: 100%;
    margin-bottom: 30px;
    line-height: 1.81;
    letter-spacing: 0.02em;

    @media (max-width: $extra-small) {
      margin-bottom: 10px;
    }
  }
}

.reviews-block__item-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;

  @media (max-width: 1600px) {
    align-items: flex-start;
  }

  .popup & {
    margin-top: 8px;
  }
}

.reviews-block__rating .rating__stars {
  width: 107px;
  height: 20px;

  @media (max-width: $extra-small) {
    width: 85px;
    height: 16px;
  }
}

.reviews-block__source {
  width: 119px;
  height: 46px;

  @media (max-width: 450px) {
    width: 76px;
    height: 29px;
  }
}

.reviews-block__source-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.reviews-block__general-link {
  margin: 0 auto;
}

.reviews-block__footer {
  display: flex;
  justify-content: center;
  margin-top: 12px;
}
