.page__container {
  width: 100%;
  height: 100%;
  max-width: 1760px;
  margin: 0 auto;
  padding: 0 120px;

  @media (max-width: $extra-large) {
    padding: 0 40px;
  }

  @media (max-width: $small) {
    padding: 0 20px;
  }

  @media (max-width: 374px) {
    padding: 0 10px;
  }

  .header &,
  .promo & {
    max-width: 1920px;
    margin: 0 auto;
    padding: 0 60px;

    @media (max-width: $extra-large) {
      padding: 0 40px;
    }

    @media (max-width: $small) {
      padding: 0 20px;
    }

    @media (max-width: 374px) {
      padding: 0 10px;
    }
  }
}
