.burger {
  position: relative;
  display: flex;
  align-items: center;
  width: 48px;
  height: 48px;
  margin-right: 41px;
  padding: 12px 6px;
  border-radius: 4px;
  transition: $main-transition;
  cursor: pointer;

  @media (max-width: $small) {
    margin-right: 20px;
  }

  @media (max-width: $small) {
    width: 27px;
    height: 27px;
    margin-right: 0;
    margin-left: 7px;
    padding: 7px 3px;
  }
}

.burger__line {
  position: relative;
  display: block;
  width: 50%;
  height: 2px;
  background: $color-text;
  transition: $main-transition;

  .burger:hover & {
    background: $color-main;
  }

  &::before,
  &::after {
    @include pseudo(200%, 2px);
    top: -12px;
    left: 0;
    background: $color-text;
    transition: $main-transition;

    @media (max-width: $small) {
      top: -7px;
    }

    .burger:hover & {
      background: $color-main;
    }
  }

  &::after {
    top: 12px;

    @media (max-width: $small) {
      top: 7px;
    }
  }

  .burger.show & {
    background: transparent;

    &::before,
    &::after {
      top: 0;
    }

    &::after {
      transform: rotate(-45deg);
    }

    &::before {
      transform: rotate(45deg);
    }
  }

  .header--main & {
    background: $white;
  }

  .header.scroll & {
    background: $color-text;
  }

  .open-menu & {
    background: $white;
  }

  .header--main &::before,
  .header--main &::after {
    background: $white;
  }

  .header.scroll &::before,
  .header.scroll &::after {
    background: $color-text;
  }

  .open-menu &::before,
  .open-menu &::after {
    background: $white;
  }

  .header.scroll .burger:hover & {
    background: $color-main;
  }

  .header.scroll .burger:hover &::before,
  .header.scroll .burger:hover &::after {
    background: $color-main;
  }

  .header.scroll .burger.show & {
    background: transparent;
  }
}
