.top-banner {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: -57px 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.top-banner__content {
  display: grid;
  grid-template-columns: 42.2% auto;
  margin-right: -90px;

  @media (max-width: $extra-large) {
    grid-template-columns: 50% auto;
    margin-right: 0;
  }

  @media (max-width: $medium) {
    display: flex;
    flex-direction: column-reverse;
  }
}

.top-banner__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 57px 0;

  @media (max-width: $medium) {
    padding: 45px 0 0;
  }

  @media (max-width: $extra-small) {
    padding: 30px 0 0;
  }
}

.top-banner__title {
  margin-bottom: 22px;

  @media (max-width: $medium) {
    margin-bottom: 16px;
  }

  @media (max-width: $extra-small) {
    margin-right: -10px;
    word-break: break-all;
  }
}

.top-banner__subtitle {
  margin-bottom: 25px;
  text-transform: none;
  letter-spacing: normal;

  @media (max-width: $small) {
    margin-bottom: 16px;
  }
}

.top-banner__text {
  max-width: 510px;
  margin-bottom: 40px;
  padding-right: 40px;

  @media (max-width: $medium) {
    margin-bottom: 30px;
  }

  @media (max-width: $extra-small) {
    padding-right: 0;
  }
}

.top-banner__images {
  display: grid;
  grid-template-columns: 29% auto;
  gap: 23px;
  align-items: flex-end;

  @media (max-width: $extra-large) {
    grid-template-columns: 35% auto;
    gap: 9px;
  }

  @media (max-width: $medium) {
    grid-template-columns: 39% auto;
  }
}

.top-banner__small-image-wrap {
  height: 340px;
  border-radius: 100px 0 0 0;
  overflow: hidden;

  @media (max-width: $large) {
    height: 280px;
  }

  @media (max-width: $extra-small) {
    height: 220px;
    border-radius: 50px 0 0 0;
  }

  @media (max-width: 450px) {
    height: 155px;
  }
}

.top-banner__big-image-wrap {
  position: relative;
  height: 513px;
  border-radius: 150px 0 0 0;
  overflow: hidden;

  @media (max-width: $large) {
    height: 430px;
  }

  @media (max-width: $extra-small) {
    height: 330px;
    border-radius: 80px 0 0 0;
  }

  @media (max-width: 450px) {
    height: 233px;
  }
}

.top-banner__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-banner__price {
  position: absolute;
  bottom: 35px;
  left: 22px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: $color-main;
  padding: 18px 48px;
  border-radius: 50px;

  @media (max-width: $extra-large) {
    bottom: 15px;
    left: 12px;
  }

  @media (max-width: $large) {
    padding: 12px 40px;
  }

  @media (max-width: $extra-small) {
    display: none;
  }
}

.top-banner__old-price {
  @include main-font(14px, 14px, 16px, 400);
  position: relative;
  color: $white;

  &::after {
    @include pseudo(100%, 50%);
    top: 1px;
    left: 0;
    border-bottom: 1px solid $white;
    transform: rotate(-7deg);
  }
}

.top-banner__new-price {
  @include main-font(25px, 20px, 1.2, 700);
  color: $white;
}
