.contact {
  display: grid;
  grid-template-columns: auto 824px;

  @media (max-width: 1500px) {
    grid-template-columns: auto 600px;
  }

  @media (max-width: $medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: $small) {
    grid-template-columns: 1fr;
    gap: 25px;
  }
}
