.bottom-footer__content {
  display: grid;
  grid-template-columns: auto 824px;
  align-items: end;

  @media (max-width: 1500px) {
    grid-template-columns: auto 600px;
  }

  @media (max-width: $medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: $small) {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  @media (max-width: $extra-small) {
    gap: 25px;
  }
}

.bottom-footer__copyright {
  @include reset();
  @include main-font(14px, 14px, 1.7, 300);
  letter-spacing: 0.02em;
  color: $white;
}

.bottom-footer__privacy {
  @include link-reset();
  @include main-font(14px, 14px, 1.7, 300);
  letter-spacing: 0.02em;
  color: $white;

  &:hover {
    color: $color-text;
  }
}

.bottom-footer__develop {
  @include reset();
  @include main-font(14px, 14px, 1.7, 300);
  color: $white;
}

.bottom-footer__develop-link {
  @include link-reset();
  display: block;
  margin-top: 5px;
}

.bottom-footer__develop-icon {
  fill: $white;
  transition: $main-transition;

  .bottom-footer__develop-link:hover & {
    fill: $color-text;
  }
}
