.about {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .promo + & {
    margin: 147px 0 180px;

    @media (max-width: $medium) {
      margin: 100px 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 60px 0 90px;
    }
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.about__content {
  display: grid;
  grid-template-columns: 56.3% auto;

  @media (max-width: $large) {
    grid-template-columns: 50% auto;
  }

  @media (max-width: $medium) {
    grid-template-columns: 1fr;
    gap: 60px;
  }

  @media (max-width: $extra-small) {
    gap: 38px;
  }
}

.about__image-wrap {
  width: 100%;
  max-width: 860px;
  height: 100%;
}

.about__image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: bottom;
}

.about__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  max-width: 480px;
  margin-left: 120px;
  padding-bottom: 36px;

  @media (max-width: $large) {
    margin-left: 60px;
    padding-bottom: 0;
  }

  @media (max-width: $medium) {
    margin-left: 0;
  }
}

.about__title {
  margin-bottom: 25px;

  @media (max-width: $extra-small) {
    margin-bottom: 20px;
  }
}

.about__text {
  margin-bottom: 30px;
}

.about__button {
  align-self: flex-start;
}
