.education {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.education__content {
  display: grid;
  grid-template-columns: 38% auto;
  gap: 120px;

  @media (max-width: $extra-large) {
    gap: 60px;
  }

  @media (max-width: $medium) {
    display: block;
  }
}

.education__slider {
  max-width: 577px;
}

.education__list {
  @include list-reset();
}

.education__link {
  @include link-reset();
}

.education__image-wrap {
  width: 100%;
  height: 100%;
}

.education__image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.education__buttons {
  position: relative;
  display: flex;
  justify-content: center;
  margin-top: 68px;

  @media (max-width: $extra-small) {
    margin-top: 26px;
  }
}

.education__button {
  @include button-reset();
  display: flex;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 35px;

  @media (max-width: $extra-small) {
    width: 35px;
    height: 21px;
  }

  &--prev {
    margin-right: 24px;

    @media (max-width: $extra-small) {
      margin-right: 15px;
    }
  }
}

.education__button-icon {
  fill: $color-main;
  transition: $main-transition;

  .education__button--prev & {
    transform: rotate(180deg);
  }

  .education__button:hover & {
    fill: $color-title;
  }
}

.education__current {
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
  color: $color-main;

  @media (max-width: $large) {
    top: -25px;
  }

  @media (max-width: $medium) {
    top: -20px;
  }
}

.education__info {
  max-width: 700px;
  padding-top: 60px;

  @media (max-width: $extra-small) {
    padding-top: 0;
  }
}

.education__info-content {
  @media (max-width: $extra-small) {
    max-height: 490px;
    overflow: hidden;
    transition: $main-transition;
  }

  &.show {
    max-height: 100vh;
  }

  &.show::after {
    display: none;
  }

  &--more {
    @media (max-width: $extra-small) {
      position: relative;

      &::after {
        @include pseudo(100%, 105px);
        background: linear-gradient(358.4deg, $white 26.18%, rgba($white, 0.1) 145.98%);
        bottom: 0;
        left: 0;
      }
    }
  }
}

.education__info-title {
  margin: 60px 0 30px;

  @media (max-width: $large) {
    margin: 40px 0 20px;
  }

  &:first-child {
    margin-top: 0;
  }
}

.education__info-text {
  letter-spacing: 0.02em;

  p {
    margin-bottom: 28px;

    @media (max-width: $large) {
      margin-bottom: 20px;
    }

    @media (max-width: $extra-small) {
      line-height: 1.45;
    }
  }
}

.education__button-more {
  display: none;

  @media (max-width: $extra-small) {
    @include button-reset();
    @include main-font(14px, 14px, 1.61, 400);
    display: block;
    margin-top: 5px;
    letter-spacing: 0.02em;
    text-decoration: underline;
    color: $color-main;
  }

  &:hover {
    color: $color-text;
  }
}
