.doctors {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.doctors__tabs-wrapper {
  display: flex;
  width: auto;
  overflow: auto;
  border-bottom: 1px solid $color-main;
  -ms-overflow-style: none;
  scrollbar-width: none;

  @media (max-width: $medium) {
    margin-right: -40px;
  }

  @media (max-width: $extra-small) {
    margin-right: -10px;
  }
}

.doctors__tabs-wrapper::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.doctors__tab-name {
  padding: 0 35px 10px;
  color: $color-text;
  border-bottom: 3px solid transparent;
  white-space: nowrap;
  cursor: pointer;
  transition: all 0.3s ease;

  @media (max-width: $medium) {
    padding: 0 30px 10px;
  }

  @media (max-width: $small) {
    padding: 0 17px 15px;
  }

  .doctors__checkbox:nth-of-type(1):checked ~ .doctors__tabs-wrapper &:nth-of-type(1),
  .doctors__checkbox:nth-of-type(2):checked ~ .doctors__tabs-wrapper &:nth-of-type(2),
  .doctors__checkbox:nth-of-type(3):checked ~ .doctors__tabs-wrapper &:nth-of-type(3),
  .doctors__checkbox:nth-of-type(4):checked ~ .doctors__tabs-wrapper &:nth-of-type(4),
  .doctors__checkbox:nth-of-type(5):checked ~ .doctors__tabs-wrapper &:nth-of-type(5) {
    color: $color-main;
    text-shadow: 0 0 0.9px $color-main, 0 0 0.9px $color-main;
    border-bottom: 3px solid $color-main;
  }
}

.doctors__tab {
  display: none;
  transition: all 0.3s ease;

  .doctors__checkbox:nth-of-type(1):checked ~ &:nth-of-type(1),
  .doctors__checkbox:nth-of-type(2):checked ~ &:nth-of-type(2),
  .doctors__checkbox:nth-of-type(3):checked ~ &:nth-of-type(3),
  .doctors__checkbox:nth-of-type(4):checked ~ &:nth-of-type(4),
  .doctors__checkbox:nth-of-type(5):checked ~ &:nth-of-type(5) {
    display: block;
  }
}

.doctors__tab-content {
  display: flex;
  flex-direction: column;
  margin-top: 87px;

  @media (max-width: $medium) {
    margin-top: 40px;
  }

  @media (max-width: $extra-small) {
    margin-top: 23px;
  }
}

.doctors__list {
  @include list-reset();
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: auto;
  gap: 80px 60px;

  @media (max-width: $large) {
    gap: 60px 50px;
  }

  @media (max-width: $medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: $extra-small) {
    grid-template-columns: 1fr;
  }
}

.doctors__button-more {
  @include button-reset();
  @include accent-font(20px, 18px, 1.6, 400);
  position: relative;
  align-self: center;
  margin-top: 70px;
  color: $color-main;
  letter-spacing: 0.155em;
  text-transform: uppercase;

  @media (max-width: $extra-small) {
    margin-top: 40px;
  }

  &::after {
    @include pseudo(100%, 1px);
    bottom: 6px;
    left: 0;
    background-color: $color-main;
  }

  &:hover {
    color: $color-text;
  }
}
