.doctor {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }
}

.doctor__content {
  display: grid;
  grid-template-columns: 45.8% auto;

  @media (max-width: $large) {
    grid-template-columns: 50% auto;
  }

  @media (max-width: $medium) {
    display: flex;
    flex-direction: column-reverse;
    gap: 60px;
  }

  @media (max-width: $small) {
    gap: 40px;
  }

  @media (max-width: $extra-small) {
    gap: 22px;
  }
}

.doctor__image-wrap {
  width: 100%;
  height: 100%;
}

.doctor__image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: bottom;
}

.doctor__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  max-width: 590px;
  margin-right: 106px;

  @media (max-width: $large) {
    margin-right: 60px;
    padding-bottom: 0;
  }

  @media (max-width: $medium) {
    margin-right: 0;
  }
}

.doctor__post {
  margin-bottom: 22px;

  @media (max-width: $small) {
    margin-bottom: 16px;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 10px;
  }
}

.doctor__name {
  text-transform: none;
  letter-spacing: normal;
  margin-bottom: 30px;

  @media (max-width: $small) {
    margin-bottom: 23px;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 17px;
  }
}

.doctor__text {
  margin-bottom: 40px;

  @media (max-width: $small) {
    margin-bottom: 30px;
  }

  @media (max-width: $extra-small) {
    margin-bottom: 20px;
  }
}

.doctor__info-footer {
  display: flex;
  align-items: center;
}

.doctor__link {
  @include link-reset();
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin-left: 30px;
  border-radius: 50%;
  background: $color-main;

  @media (max-width: $extra-small) {
    margin-left: 20px;
  }

  &:hover {
    background: $color-text;
  }
}

.doctor__link-icon {
  fill: $white;
}
