.top-footer {
  padding-bottom: 27px;

  @media (max-width: $small) {
    padding-bottom: 20px;
  }
}

.top-footer__content {
  display: grid;
  grid-template-columns: auto 824px;
  align-items: center;

  @media (max-width: 1500px) {
    grid-template-columns: auto 600px;
  }

  @media (max-width: $medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: $small) {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  @media (max-width: $extra-small) {
    gap: 0;
  }
}

.top-footer__text {
  @include reset();
  @include main-font(14px, 12px, 1.7, 400);
  max-width: 80%;
  letter-spacing: 0.02em;
  color: rgba($white, 0.6);

  @media (max-width: $extra-large) {
    max-width: 90%;
  }

  @media (max-width: $small) {
    max-width: 80%;
    margin-top: 36px;
  }

  @media (max-width: $extra-small) {
    max-width: 100%;
    line-height: 15px;
  }
}

.top-footer__logo-image {
  max-width: 282px;
  max-height: 39px;

  @media (max-width: $extra-small) {
    max-width: 167px;
    max-height: 23px;
  }
}
