.blog-article {
  margin: 180px 0;

  @media (max-width: $medium) {
    margin: 120px 0;
  }

  @media (max-width: $extra-small) {
    margin: 90px 0;
  }

  .breadcrumbs + & {
    margin: 0 0 180px;

    @media (max-width: $medium) {
      margin: 0 0 120px;
    }

    @media (max-width: $extra-small) {
      margin: 0 0 90px;
    }
  }

  .top-block + & {
    margin-top: -40px;

    @media (max-width: $large) {
      margin-top: -50px;
    }
  }

  p {
    @include reset();
    @include main-font(16px, 14px, 1.66, 400);
    margin-bottom: 28px;
    color: $color-text;
    letter-spacing: 0.02em;

    @media (max-width: $small) {
      margin-bottom: 38px;
      line-height: 1.45;
      letter-spacing: normal;
    }
  }

  ul {
    @include list-reset();
  }

  li {
    @include main-font(16px, 14px, 1.61, 400);
    position: relative;
    letter-spacing: 0.02em;
    padding-left: 26px;

    @media (max-width: $small) {
      padding-left: 20px;
      line-height: 1.45;
      letter-spacing: normal;
    }

    &:not(:last-child) {
      margin-bottom: 15px;
    }

    &::before {
      @include pseudo(12px, 12px);
      top: 6px;
      left: 0;
      border: 1px solid #4a4a4a;
      border-radius: 50%;

      @media (max-width: $small) {
        @include pseudo(10px, 10px);
      }
    }
  }
}

.blog-article__content {
  max-width: 850px;
  margin: 0 auto;
}

.blog-article__quote {
  width: 95%;
  margin: 140px 0 100px;
  font-style: italic;

  @media (max-width: $large) {
    width: 100%;
    margin: 100px 0 60px;
  }

  @media (max-width: $small) {
    margin: 100px 0 40px;
  }

  p {
    @include main-font(20px, 14px, 1.61, 400);
    letter-spacing: 0.02em;
  }
}

p.blog-article__title {
  @include accent-font(60px, 26px, 1.1, 400);
  margin: 100px 0 40px;
  letter-spacing: normal;

  @media (max-width: $large) {
    margin: 60px 0 30px;
  }

  @media (max-width: $small) {
    margin: 40px 0 20px;
  }
}

p.blog-article__list-title {
  @include accent-font(30px, 20px, 1.1, 400);
  margin: 40px 0 32px;
  letter-spacing: normal;

  @media (max-width: $small) {
    margin: 30px 0 20px;
  }
}

.blog-article__image-wrap {
  width: 100%;
  margin: 60px 0;

  @media (max-width: $small) {
    margin: 30px 0 25px;
  }
}

.blog-article__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

p.blog-article__subtitle {
  @include accent-font(30px, 20px, 1.1, 400);
  margin: 100px 0 30px;
  text-transform: uppercase;
  letter-spacing: 0.04em;

  @media (max-width: $large) {
    margin: 60px 0 30px;
  }

  @media (max-width: $small) {
    margin: 25px 0 20px;
    letter-spacing: 0.02em;
  }
}

.blog-article__author {
  position: relative;
  margin-top: 100px;
  padding-left: 88px;

  @media (max-width: $large) {
    margin-top: 60px;
  }

  @media (max-width: $extra-small) {
    padding-left: 76px;
  }
}

.blog-article__author-image-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 69px;
  height: 69px;
  border-radius: 50%;
  overflow: hidden;

  @media (max-width: $extra-small) {
    width: 56px;
    height: 56px;
  }
}

.blog-article__author-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

p.blog-article__author-title {
  @include main-font(20px, 16px, 1.61, 400);
  margin-bottom: 18px;
  letter-spacing: 0.02em;
  color: #9f9f9f;

  @media (max-width: $extra-small) {
    margin-bottom: 10px;
  }
}

p.blog-article__author-post {
  @include accent-font(30px, 20px, 1.1, 400);
  margin-bottom: 3px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $color-text;

  @media (max-width: $extra-small) {
    margin-bottom: 8px;
    letter-spacing: 0.02em;
  }
}

p.blog-article__author-name {
  @include main-font(20px, 16px, 1.61, 400);
  margin-bottom: 0;
  letter-spacing: 0.02em;
  color: $color-text;
}
